// best-service

.best-service {
    h2{
        margin-top: 0;
    }
    margin-bottom: 70px;
    @include media-breakpoint-down(lg) {
      margin-bottom: 60px;
    }
    @include media-breakpoint-down(md) {
      margin-bottom: 50px;
    }
}  
.best-service__text{
    ul{
        margin-top: 30px;
        margin-bottom: 30px;
        @include media-breakpoint-up(md) {
            margin-top: 40px;
            margin-bottom: 50px;
        }
        &:last-child{
            margin-bottom: 0;
        }
        li{
            position: relative;
            list-style: none;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 1.78571em;
            max-width: 972px;
            margin-bottom: 14px;
            margin-top: 14px;
            padding-left: 1.3em;
            @include media-breakpoint-up(sm) {
                font-size: 16px;
                line-height: 1.8125em;
            }
            @include media-breakpoint-only(lg) {
                font-size: 14px;
                line-height: 22px;
            }
            &:before {
                content: "";
                position: absolute;
                display: inline-block;
                height: 5px;
                width: 5px;
                border-radius: 50%;
                background-color: $main-yellow;
                left: 0;
                top: .65em;
            }        
        }
    }
}
.best-service__subtitle {
    margin-top: 14px;
    & + p{
        margin-bottom: 0;
    }
}

.best-service__list {
    margin-top: 10px;
}

.best-service__item {
    margin-bottom: 20px;
}

.best-service__link {
    margin-top: 23px;
}

.bid__link{
    color: black;
    &:hover{
        color: black;
    }
}