.block-request-calculation{
    margin-top: 70px;
    padding-top: 0;
    border-radius: $border-radius;
    @include media-breakpoint-down(lg) {
        margin-top: 60px;
    }
    @include media-breakpoint-down(md) {
        margin-top: 50px;
    }
    @include media-breakpoint-down(sm) {
        width: calc(100% - 30px);
    }
    @include media-breakpoint-down(xs) {
        width: auto
    }
  
    @include media-breakpoint-up(md) {
      border-color: $main-light-grey;
    }
  
    @include media-breakpoint-up(lg) {
      background-size: 250px;
    }
    .calculation__content{
        @include media-breakpoint-up(md) {
            margin-left: -15px;
            margin-right: -15px;   
        }
        @include media-breakpoint-down(xs) {
            margin-left: -20px;
            margin-right: -20px;   
        }
        background-color: $white;
        border-radius: $border-radius;
    }
    .answer-question--rounded{
        padding: 0;
        @include media-breakpoint-down(md) {
            width: calc(100% + 30px);
            margin-left: -15px;
        }
        @include media-breakpoint-down(xs) {
            width: 100%;
            margin-left: 0;
        }
    }
}
.calculation-top{
    .calculation__title{
        padding-left: 44px;
        max-width: none;
        min-height: 85px;
        background-color: #EDEDF1;
        display: flex;
        align-items: center;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        margin-top: 0;
        margin-bottom: 38px;
        padding-top: 25px;
        padding-bottom: 25px;
        @include media-breakpoint-down(sm) {
            padding-left: 24px;
            padding-right: 24px;
        }
    }
}

.calculation__block-title{
    height: 85px;
    background-color: #EDEDF1;
    .container{
        margin-top: auto;
        margin-bottom: auto;
    }
}
.calculation-services{
    padding-left: 43px;
    padding-right: 43px;
    @include media-breakpoint-down(sm) {
        padding-left: 24px;
        padding-right: 24px;
    }
    display: flex;
    flex-wrap: wrap;
    .form__contacts{
        display: flex;
        width: 100%;
        @include media-breakpoint-down(sm) {
            flex-direction: column;
            .input + .input{
                margin-left: 0;
            }
        }
    }
    .form__radio-block{
        display: flex;
        width: 100%;
        @include media-breakpoint-down(sm) {
            flex-direction: column;
        }
        .radio-block{
            width: 100%;
            margin-top: 40px;
            + .radio-block{
                margin-left: 25px;
            }
            @include media-breakpoint-down(sm) {
                + .radio-block{
                    margin-left: 0;
                }
            }
            .radio-buttons{
                display: flex;
                flex-direction: column;
                label:not(:last-child){
                    margin-bottom: 6px;
                }
            }
            .radio-block__title{
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: $main-black;
                margin-bottom: 12px;
                margin-top: 0;
            }
            input{
                -webkit-appearance: radio;
                appearance: radio;

                position: absolute;
                z-index: -1;
                opacity: 0;
                margin: 10px 0 0 7px;
            }
            input + label {
                position: relative;
                padding: 0 0 0 27px;
                cursor: pointer;
            }
            input + label:before {
                content: '';
                position: absolute;
                top: 2px;
                left: 0;
                width: 17px;
                height: 17px;
                border: 1px solid #CDD1DA;
                border-radius: 50%;
                background: $white;
            }
            input + label:after {
                content: '';
                position: absolute;
                top: 6px;
                left: 4px;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                background: $main-yellow;
                opacity: 0;
                transition: .2s;
            }
            input:checked + label:after {
                opacity: 1;
            }
        }
    }
    .form__subject{
        width: 100%;
        margin-top: 40px;
        margin-bottom: 40px;
        label{
            margin-top: 0;
            width: calc(50% - 10px);
            @include media-breakpoint-down(sm) {
                width: 100%;
            }
        }
        .subject-description{
            display: block;
            margin-top: 12px;
            color: $main-dark-grey;
        }
    }
    .form__bottom{
        height: 117px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        hr{
            position: relative;
            width: calc(100% + 86px);
            left: -43px;
            margin: 0;
            @include media-breakpoint-down(sm) {
                width: calc(100% + 48px);
                left: -24px;
            }
        }
    }
    .input{
        flex: 1 0 auto;
        max-width: none;
        margin-bottom: 0;
        input{
            width: 100%;
        }
        + .input{
            margin-left: 20px;
        }
        &__error{
            padding-top: 6px;
        }
    }
    .select-container{
        flex-basis: 100%;
        margin-top: 33px;
        position: relative;
    }
    .textarea-container{
        flex-basis: 100%;
        position: relative;
        margin-bottom: 23px;
        .input__error{
            padding-top: 0;
        }
        .input__placeholder{
            top: 10px;
        }
    }
    .input-name{

    }
    .input-email{

    }
    .input-question{
        width: 100%;
        margin-top: 16px;
        height: 137px;
        padding-left: 16px;
        padding-right: 8px;
        padding-top: 13px;
        &.error {
          border-color: $input-error;

          &+.input__placeholder {
            color: $input-error;
          }
          &~.input__error {
            display: block;
          }
        }
      
        &.valid {
          &~.input__error {
            display: none;
          }
        }
    }
    .input-question:focus + .input__placeholder{
        transform-origin: 0 0;
        top: 15px;
        transform: translate(-1px, -5px) scale(0.88);
    }
    .input__placeholder--acrive + .input__placeholder{
        transform-origin: 0 0;
        top: 15px;
        transform: translate(-1px, -5px) scale(0.88);
    }
    &__select{
        width: 100%;
        padding-left: 16px;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        background: url('images/calculation-services-arrow.svg');
        background-repeat: no-repeat;
        background-size: 16px 17px;
        background-position: calc(100% - 20px);
        &+.input__placeholder {
            transform-origin: 0 0;
            transform: translate(-1px, -5px) scale(0.88);
        }
    }
    &__agreement{
        font-family: Roboto;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 180%;
        color: $main-dark-grey;
    }
    .link-bottom{
        max-width: 361px;
        margin-left: 30px;
        @media (max-width: 600px) {
            max-width: 300px;
        }
    }
    @include media-breakpoint-down(ssm) {
        .input{
            min-width: 100%;
            input{
            }
            + .input{
                margin-left: 0px;
            }
        }
        .calculation-services__submit{
            width: 100%;
        }
        .link-bottom{
            max-width: none;
            width: 100%;
            margin-left: 0;
            margin-top: 24px;
        }
    }
}
.select__subject{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    color: #192630;
    height: 42px;
}
.calculation-services__input{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 180%;
    height: 56px;
    color: $main-dark-grey;
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
}
.calculation-services__textarea{
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
}
.calculation-services__select{
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
    height: 56px;
}
.calculation-services__submit{
    margin-top: auto;
    margin-bottom: auto;
}




.calculation-services{
    *,
*:before,
*:after{
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.select {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    height: 56px;
    /* Light grey */
    & + .input__placeholder{
        top: -27px;
        z-index: 10;
    }
}

.select-wrap {
	max-width: 700px;
	width: 100%;
	margin: 20px auto;
}

.select-wrap select {
	margin: 20px;
}

.select__gap {
	background-color: $white;
	cursor: pointer;
    position: relative;
    height: 100%;

    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 180%;
    display: flex;
    align-items: center;
    width: 100%;
    padding-left: 16px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
    z-index: 8;
}

.select__list {
    background: $white;
    background-color: $white;
    position: absolute;
    z-index: 5;
    width: 100%;
    padding-top: 15px;
    margin-top: -10px;

    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
}

.select__list.on {
	display: block;
}

.select__item span {
    display: block;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 15px;
	cursor: pointer;
    color: #333;
}

.select__item:nth-child(2n){
    background-color: #F3F4F5;
}
.select__item:hover {
    background-color: $light-bg-color;
}
.select__gap:after {
	content: '';
	display: block;
	position: absolute;
    
    background-image: url('images/calculation-services-arrow.svg');
    background-repeat: no-repeat;
    background-size: 16px 17px;
    width: 16px;
    height: 17px;
    right: 20px;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;
    -webkit-transition: all .27s ease-in-out;
	     -o-transition: all .27s ease-in-out;
	        transition: all .27s ease-in-out;

	-webkit-transform: rotate(0deg);
	    -ms-transform: rotate(0deg);
	     -o-transform: rotate(0deg);
            transform: rotate(0deg);
}

.on.select__gap:after {
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	     -o-transform: rotate(180deg);
	        transform: rotate(180deg);
}
}