// nav-responses

.nav-responses {
  // padding: 40px 30px 40px 30px;
  // padding: 40px 40px 40px 30px;
  padding: 40px 40px 40px 0;



  // position: absolute;
  // top: 0;
  // left: 0;
  width: $nav-responses-width-lg;



  @include media-breakpoint-up(xl) {
    // padding-right: 40px;
    width: $nav-responses-width-xl;
  }

  .nav-responses__list {
    margin-top: 0;
    margin-bottom: 0;

    li {
      padding-left: 53px;

      @include media-breakpoint-up(xl) {
        font-size: em(18px, 16px);
        line-height: em(25px, 18px);
        margin-bottom: .9em;
      }

      &:first-of-type {
        margin-top: 0;
      }

      &:last-of-type {
        margin-bottom: 0;
      }

      &::before {
        left: 30px;
      }

      &::after {
        content: '';
        width: 4px;
        height: 100%;
        display: block;
        background-color: transparent;
        border-radius: 0px 2px 2px 0px;
        position: absolute;
        left: 0;
        top: 0;

        @include animateBgcolor;
      }

      &.active {
        position: relative;

        &::after {
          background-color: $main-yellow;

        }
      }
    }

  }


  position: -webkit-sticky;
  position: sticky;
  // top: 84px;
  // top: 140px;
  top: 154px;
}

.nav-responses__link {
  color: $black;
  font-weight: 500;

  @include media-breakpoint-only(lg) {
    font-size: 16px;
  }
}