.modal-city {
  width: 100%;
  // height: 100%;
  align-items: center;
  // font-weight: 500;

  .modal__inner {
    width: 100%;
  }
}

.modal-city__head {
  margin-right: 22px;

  @include media-breakpoint-up(sm) {
    margin-right: 52px;
  }
}

.modal-city__title {
  @include h2-style;
  margin-top: 10px;
  margin-bottom: 25px;
}

.modal-city__text {
  margin-bottom: 24px;

  @include media-breakpoint-up(sm) {
    margin-bottom: 33px;
  }
}

.city__input {
  max-width: 100%;
  width: 100%;

  .input__fluid {
    width: 100%;
    max-width: 100%;
  }
}


//*** Города

.cities {
  margin-top: 24px;
  position: relative;

  @include media-breakpoint-up(md) {
    margin-top: 47px;
  }

  &:after,
  &:before {
    content: '';
    display: block;
    width: 100%;
    position: absolute;
    left: 0;
    height: 24px;
    pointer-events: none;
    opacity: 0;

    @include animateOpacity;

    @include media-breakpoint-up(md) {
      height: 80px;
    }

    @include media-breakpoint-up(xl) {
      height: 145px;
    }
  }

  &:after {
    bottom: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, $white 100%);
  }

  &:before {
    top: 0;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0) 0%, $white 100%);
    z-index: 999;
  }
}

.scroll-gradient {
  &--bottom {
    &:after {
      opacity: 1;
    }
  }

  &--top {
    &:before {
      opacity: 1;
    }
  }
}

.cities__scroll {
  position: relative;
  -webkit-transform: translateZ(0px);
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  display: block;
  font-size: 14px;
  line-height: em(25px, 14px);

  height: 280px;
  // height: 100%;
  max-height: 100%;

  @include media-breakpoint-up(sm) {
    font-size: 16px;
    line-height: em(29px, 16px);

    height: 327px;
    // height: 100%;
    // max-height: 100%;
  }
}

.cities__list {
  // height: calc(100% + 1px); // *** TEST
  -webkit-overflow-scrolling: touch;
  margin-top: 0;
  padding-right: 20px;
  // margin-bottom: 60px;
  margin-bottom: 10px;

  @include media-breakpoint-up(sm) {
    column-count: 2;
  }

  @include media-breakpoint-up(md) {
    column-count: 3;
    padding-right: 35px;
  }

  @include media-breakpoint-up(lg) {
    padding-right: 105px;
  }
}

.cities__item {
  margin-bottom: 40px;
  column-width: 45%;
  display: block;

  display: flex;
  flex-direction: column;


  break-inside: avoid; // нельзя разравать список по разным колонкам

  @include media-breakpoint-up(md) {
    column-width: 26.5%;
    max-width: 250px;
  }
}

.cities__city {
  color: $black;
}


.cities__char {
  font-weight: bold;
  font-size: 24px;
  line-height: em(34px, 24px);
  color: $modal-city-char-color;
  margin-top: 0;
  margin-bottom: 16px;
  user-select: none;
}