// content-block

.content-block {
  margin-bottom: 54px;

  h2 {
    @include media-breakpoint-up(lg) {
      margin-bottom: 40px;
    }
  }

  h1 {
    @include media-breakpoint-up(lg) {
      margin-bottom: 40px;
    }
  }
  .main-title{
      margin-top: 50px;
      margin-bottom: 30px;
  
      @include media-breakpoint-up(md) {
        margin-top: 71px;
      }

      font-weight: 700;
    font-size: 24px;
    line-height: em(28px, 24px);
    max-width: 689px;
    margin-bottom: 30px;

    @include media-breakpoint-up(sm) {
      font-size: 34px;
      line-height: em(40px, 34px);
    }

    @include media-breakpoint-only(lg) {
      font-size: $h2-size-lg;
    }
  }

  p {
    @include media-breakpoint-up(lg) {
      margin-bottom: 16px;
    }

    @include media-breakpoint-only(lg) {
      font-size: 14px;
    }
  }
}