.teacher-block{
    h2{
        margin-top: 0;
        margin-bottom: 40px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 30px;
        }
    }
    .teacher-content{

    }
    .teacher__description{
        background-color: $main-white;
        border: 1px solid $main-light-grey;
        border-radius: $border-radius;
        display: flex;
        padding-left: 0;
        padding-right: 0;
        @include media-breakpoint-down(md) {
            flex-direction: column;
        }
        .description-img{
            background-color: #EDEDF1;
            display: flex;
            align-items: flex-end;
            img{
                margin-left: auto;
                margin-right: auto;
                width: auto;
                max-width: none;
                position: relative;
                margin-right: 0;
                @include media-breakpoint-down(md) {
                    min-height: auto;
                    margin-right: auto;
                }
            }
            @include media-breakpoint-only(xl) {
                width: 304px;
                max-width: 304px;
                min-width: 304px;
                img{
                    margin-right: auto;
                    width: 94%;
                }
            }
            @include media-breakpoint-only(lg) {
                width: 304px;
                max-width: 304px;
                min-width: 304px;
                img{
                    margin-right: auto;
                    width: 81%;
                }
            }
            @include media-breakpoint-only(md) {
                height: 260px;
            }
            @include media-breakpoint-only(sm) {
                height: 263px;
            }
            @include media-breakpoint-down(ssm) {
                height: 220px;
                img{
                    height: 98% !important;
                }
            }

            @include media-breakpoint-down(md) {
                img{
                    height: 100%;
                }
            }
            
        }
        .description-text{
            padding: 50px 40px 50px 44px;
            @include media-breakpoint-down(ssm) {
                padding: 30px 24px 30px 24px;
            }
            .description__name{
                margin-top: 0;
                margin-bottom: 24px;
            }
            ul{
                margin: 0;
                li{
                    color: $main-dark-grey;
                    padding-left: 1.3em;
                    font-size: 1em;
                    position: relative;
                    list-style: none;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 1.78571em;
                    max-width: 972px;
                    @media (min-width: 576px) {
                        font-size: 16px;
                        line-height: 1.8125em;
                    }
                    &:first-child{
                        margin-top: 0;
                    }
                    &:last-child{
                        margin-bottom: 0;
                    }
                    &:before {
                        content: "";
                        position: absolute;
                        display: inline-block;
                        height: 5px;
                        width: 5px;
                        border-radius: 50%;
                        background-color: $main-yellow;
                        left: 0;
                        top: .8em;
                    }
                }
            }

        }
        .description__name{

        }
    }
    .teacher__licenses{
        height: 100%;
        border: 1px solid $main-light-grey;
        border-radius: $border-radius;
        padding: 60px 50px 40px 45px;
        @include media-breakpoint-down(lg) {
            margin-top: 20px;
            padding: 50px 50px 50px 45px;
        }
        @include media-breakpoint-down(ssm) {
            padding: 30px 24px 30px 24px;
        }
        h3{
            margin-bottom: 37px;
            @include media-breakpoint-down(lg) {
                margin-bottom: 30px;
            }
        }
        ul{
            margin: 0;
            display: block;
            li{
                width: 100%;
                margin: 0;
            }
        }
        .icon-link__text{
            @include media-breakpoint-only(lg) {
                margin-top: 10px;
            }
            @include media-breakpoint-only(md) {
                margin-top: 3px;
            }
        }
        .icon-link__icon-wp{
            width: 54px;
            height: 64px;
        }
    }
}