// faq-item


.faq-item {
  h3 {
    margin-top: 0;
    margin-bottom: 0.7em;
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
    display: inline;

    &>a {
      color: #5C6D99;

      &:active {
        color: $link-color-hover;
      }

      @include media-breakpoint-up(sm) {

        &:hover,
        &:focus {
          color: $link-color-hover;
        }
      }
    }
  }

  margin-bottom: 40px;

  @include media-breakpoint-up(sm) {
    margin-bottom: 53px;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.faq-item__text {
  display: block;
  overflow: hidden;
  margin-bottom: 16px;
  line-height: 1.8125em;


}