.customers-carousel{
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 70px;
    @include media-breakpoint-down(lg) {
        margin-bottom: 60px;
    }
    @include media-breakpoint-down(md) {
        margin-bottom: 50px;
    }
    h2{
        margin-top: 0;
        margin-bottom: 0;
    }
    .carousel-content{
        display: flex;
        margin-top: 40px;
        align-items: center;
        margin-left: -16px;
        margin-right: -16px;
        @include media-breakpoint-down(md) {
            width: 90%;
            margin-left: auto;
            margin-right: auto;
        }
        @include media-breakpoint-down(sm) {
            margin-top: 30px;
        }
    }
    .carousel-item{
        background: $white;
        border: 0.990937px solid $border-color;
        box-sizing: border-box;
        border-radius: 9.9px;
        width: 304px;
        height: 173px;
        padding-left: 30px;
        padding-right: 30px;
        display: flex !important;
        img{
            margin: auto;
            max-height: 85%;
        }
    }
    .slick-slide{
        margin-right: 16px;
        margin-left: 16px;
    }

}
.partners-arrow-left, .partners-arrow-right{
    position: absolute;
    cursor: pointer;
    stroke: $main-dark-grey;
    &:hover{
        stroke: $main-yellow;
    }
}