// info-block

.info-block {
  position: relative;
  background-color: $white;
  border: 1px solid $main-light-grey;
  border-radius: $border-radius;
  overflow: hidden;
  height: 100%;

  padding: 30px 24px 27px 30px;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 5px;
    background-color: $main-yellow;
  }
}

.info-block__title {
  @include h4-style();
  margin-top: 0;

  @include media-breakpoint-only(lg) {
    font-size: $subtitle-size-lg;
  }
}

.info-block__text {
  margin-bottom: 0;
  color: $main-dark-grey;
  line-height: em(22px, 16px);
  
  &:first-of-type {
    margin-top: 1.5em;
  }
  &:last-of-type {
    small {
      margin-bottom: 0;
    }
  }

  // small {
  //   font-size: 1em;
  // }

  small {
    margin-bottom: 16px;
  }
}