// page-switch

.page-switch {
  height: $page-switch-height;
  overflow: hidden;
  margin-top: 50px;
  margin-bottom: 50px;

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
    margin-bottom: 70px;
  }
}

.page-switch__inner {

  @include media-breakpoint-only(md) {
    max-width: 100%;
  }
}

.page-switch__list {
  margin-top: 0;
  margin-bottom: 0;
  overflow-x: auto;
  overflow-y: hidden;
  height: $page-switch-list-height;
  max-height: $page-switch-list-height;
  display: flex;
  align-items: flex-start;


  @include media-breakpoint-only(md) {
    padding-left: (calc((100vw - 707px) / 2 + 10px));
  }
}

.page-switch__item {

  &:last-of-type {
    &>.page-switch__link {
      margin-right: 0;

      @include media-breakpoint-down(md) {
        margin-right: $grid-gutter-width / 2;
      }
    }
  }
}

.page-switch__link {
  @include h4-style();
  margin-top: 0;
  margin-bottom: 0;
  white-space: nowrap;
  display: block;
  // padding-bottom: 9px;
  margin-right: 30px;
  font-weight: 500;
  position: relative;

  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $active-item-border-color;
    bottom: -10px;
    left: 0;
    opacity: 0;
    pointer-events: none;
    @include animateOpacity;
  }

  &.active {
    // border-bottom: 2px solid $active-item-border-color;
    color: $active-item-color;

    &:after {
      opacity: 1;
    }
  }
}