// epc-bidding-subpage


.epc-bidding__subpage {

  &>section:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  &>section:last-child {
    // margin-bottom: 35px;
    margin-bottom: 46px;
  }

  &>.modern-approach:first-child {
    margin-bottom: 50px;

    @include media-breakpoint-up(md) {
      margin-bottom: 71px;
    }
  }

  &>.offers:first-child {
    &>.offers__list {
      margin-top: 0;
    }
  }

  .search-steps {
    margin-top: 0;

    .cost-calc {
      margin-top: 0;
    }
  }

  .subscribe-newsletter-wp {
    margin-top: 0;
  }
}


.epc-bidding__content {

  &>a,
  &>p>a {
    color: $link-color-2;

    &:active {
      color: $link-color-hover;
    }

    @include media-breakpoint-up(sm) {

      &:hover,
      &:focus {
        color: $link-color-hover;
      }
    }
  }

  &>h2 {

    &:first-child {
      margin-top: 0;

      @include media-breakpoint-up(xl) {
        margin-bottom: 40px;
      }
    }
  }

  &>h3 {
    max-width: 100%;
  }

  &>p+h3 {

    @include media-breakpoint-up(md) {
      margin-top: 60px;
    }
  }
}