    }
  }
}} */

@media not all and (min-resolution:.001dpcm) and (max-width: 767.98px) and (min-width: 576px)
{ @supports (-webkit-appearance:none) {
  @media (max-width: 767.98px) and (min-width: 576px){
    .card__img-wp .card__img{
      height: 100%;
      min-width: auto;
      max-width: 125%;
      width: 125%;
      position: relative;
    }
  }
}}


@media not all and (min-resolution:.001dpcm) and (max-width: 575.98px) and (min-width: 400px)
{ @supports (-webkit-appearance:none) {
  @media (max-width: 575.98px) and (min-width: 500.98px){
    .card__img-wp .card__img{
      height: 140%;
      min-width: auto;
      max-width: 100%;
      width: 100%;
      position: relative;
    }
  }
}}

@media not all and (min-resolution:.001dpcm) and (max-width: 575.98px) and (min-width: 400px)
{ @supports (-webkit-appearance:none) {
  @media (max-width: 500px) and (min-width: 400px){
    .card__img-wp .card__img{
      height: 130%;
      min-width: auto;
      max-width: 120%;
      width: 120%;
      position: relative;
    }
  }
}}

@media not all and (min-resolution:.001dpcm) and (max-width: 399.98px)
{ @supports (-webkit-appearance:none) {
  @media (max-width: 399.98px){
    .card__img-wp .card__img{
      height: 106%;
      min-width: auto;
      max-width: 120%;
      width: 120%;
      position: relative;
    }
  }
}}
@media not all and (min-resolution:.001dpcm) and (max-width: 399.98px)
{ @supports (-webkit-appearance:none) {
  @media (max-width: 350px){
    .card__img-wp .card__img{
      height: 106%;
      min-width: auto;
      max-width: 140%;
      width: 140%;
      position: relative;
    }
  }
}}


// *********************
.card__content {
  padding: 30px 24px 30px 24px;
  border: 1px solid $card-border-color;
  border-radius: 0 0 $card-border-radius $card-border-radius;
  background-color: $white;
  height: calc(100% - 137px);

  @include media-breakpoint-only(lg) {
    padding: 20px;
  }

  @include media-breakpoint-up(xl) {
    height: calc(100% - 200px);

  }
}

.card__title {
  @include h4-style();
  color: $card-title-color;
  margin-top: 0;
  margin-bottom: em(24px, $subtitle-size-xl);

  @include media-breakpoint-up(sm) {
    margin-bottom: em(20px, $subtitle-size-xl);
  }

  @include media-breakpoint-only(lg) {
    font-size: $subtitle-size-lg;
  }

  @include media-breakpoint-up(xl) {
    margin-bottom: em(23px, $subtitle-size-xl);
    font-size: $subtitle-size-xl;
  }
}

.card__title-part {

  @include media-breakpoint-up(md) {
    display: block;
  }
}

.card__text-wp {
  margin-bottom: 0;
  margin-top: 0;
  line-height: em(22px, 16px);
}

.card__text {
  color: $card-text-color;
  font-size: $p-all-size-xs;
  margin-bottom: 0;

  @include media-breakpoint-only(md) {
    font-size: $p2-size-md;
  }

  @include media-breakpoint-only(lg) {
    font-size: $p2-size-lg;
  }

  @include media-breakpoint-up(xl) {
    font-size: $p2-size-xl;
  }
}