// main-slider

.main-slider {
  margin-bottom: 43px;
  margin-top: 33px;
  overflow-x: hidden;

  @include media-breakpoint-up(sm) {
    margin-top: 40px;
    margin-bottom: 60px;
  }

  @include media-breakpoint-up(lg) {
    margin-top: 34px;
    margin-bottom: 46px;
  }

  @include media-breakpoint-up(xl) {
    margin-bottom: 20px;
    margin-top: 12px;
  }
}

.main-slider__inner {
  @include media-breakpoint-only(xs) {
    max-width: 100%;
    padding-left: $grid-gutter-width-xs / 2;
    margin-left: $grid-gutter-width-xs / 2;
    margin-right: - ($grid-gutter-width-xs / 2);
  }

  @include media-breakpoint-between(ssm, sm) {
    max-width: 100%;
    padding-left: $grid-gutter-width / 2;
    margin-left: $grid-gutter-width / 2;
    margin-right: - ($grid-gutter-width / 2);
  }

  @include media-breakpoint-only(md) {
    max-width: 100%;
    margin-left: (calc((100vw - 707px) / 2));
    margin-right: -(calc((100vw - 707px) / 2));
  }
}

.main-slider__list-wp {
  position: relative;

  @include media-breakpoint-between(ssm, sm) {
    padding: 0;
  }
}

.main-slider__list {
  margin-top: 0;
  margin-bottom: 0;

  @include media-breakpoint-up(lg) {
    // mask-image: url(../images/slider-mask.svg);
    mask-image: url('images/slider-mask.svg');
    mask-repeat: no-repeat;
    mask-position: center;
    mask-size: 99% 99%;

    -webkit-mask-type: alpha;
    mask-type: alpha;

    -webkit-mask-mode: unset;
    mask-mode: unset;
  }

  .slick-track {
    display: flex;

    .slick-slide {
      display: flex;
      height: auto;

      &>div {
        width: 100%;
      }
    }
  }
}

.main-slider__item {
  display: block;
  height: 100%;

  @include media-breakpoint-down(sm) {
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 10px;
  }

  @include media-breakpoint-up(lg) {
    max-width: 100%;
  }

  @include media-breakpoint-only(md) {
    margin-right: 20px;
  }
}

.main-slider__content-inner {
  width: 100%;
}

//*** Стили дял контента главного сладйер и главного баннера
.main-block__content {
  // *** чтобы кнопка была снизу
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  // *** чтобы кнопка была снизу

  //* Заголовок
  h1,
  h2,
  h3 {
    font-size: 30px;
    line-height: 35px;
    margin-top: 0;
    margin-bottom: 24px;
    // max-width: 232px;

    @include media-breakpoint-up(md) {
      // max-width: 284px;
    }

    @include media-breakpoint-up(xl) {
      // max-width: 416px;
      font-size: 44px;
      line-height: 52px;
      margin-bottom: 23px;
    }
  }

  //*** Текст (подзаголовок)
  p {
    // font-size: $p-all-size-xs;
    line-height: em(20px, $p-all-size-xs);
    margin-bottom: 35px;

    max-width: 232px;

    @include media-breakpoint-down(sm) {
      margin-top: 0;
    }

    @include media-breakpoint-up(md) {
      max-width: 333px;
      margin-bottom: 40px;
    }

    @include media-breakpoint-up(lg) {
      max-width: 276px;
      margin-bottom: 30px;
    }

    @include media-breakpoint-up(xl) {
      // max-width: 416px;
      max-width: 380px;
      // font-size: $p2-size-xl;
      line-height: em(29px, $p2-size-xl);
    }
  }

  //*** Ссылка, похожая на кнопку
  a {
    // btn-primary 
    width: 100%;
    // margin-top: auto;
    // margin-bottom: 0;

    @include media-breakpoint-up(ssm) {
      width: auto;
    }

    @include media-breakpoint-only(md) {
      font-size: 12px;
      // min-height: 45px;
      padding-top: 14px;
      padding-bottom: 14px;
    }

    @include media-breakpoint-only(lg) {
      font-size: 11px;
      // min-height: 38px;
      padding-top: 12px;
      padding-bottom: 12px;
    }

    &:hover,
    &:active {
      color: $black;
    }

    &.link-primary {

      &:hover,
      &:active {
        @include link-style-primary;
      }
    }
  }

  // **** ОТСТУПЫ И ВЫСОТА
  padding-top: 70px;
  padding-bottom: 70px;
  min-height: 422px;
  width: 100%;
  max-width: 100%;

  @include media-breakpoint-up(sm) {
    min-height: 359px;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include media-breakpoint-up(lg) {
    min-height: 355px;
    padding-top: 56px;
    // padding-bottom: 42px;
    padding-bottom: 56px;
  }

  @include media-breakpoint-up(xl) {
    // min-height: 525px;
    min-height: 500px;
    // padding-top: 114px;
    // padding-bottom: 114px;
    padding-top: 79px;
    padding-bottom: 79px;
  }
}


// **** СЛАЙДЕР
.main-slider__content {
  height: 100%;
  width: 100%;
  color: $white;
  overflow: hidden;
  border-radius: 6px;
  position: relative;

  // ***************************************** NEW

  display: flex;
  padding-top: 70px;
  padding-bottom: 70px;
  justify-content: center; // *** контент слайдера будет по центру

  @include media-breakpoint-up(md) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include media-breakpoint-up(lg) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @include media-breakpoint-up(xl) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  a {
    margin-top: 0;
    margin-bottom: auto;
  }

  // ************************************************* NEW

  &:after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    display: block;
    background-color: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;
    z-index: -1;
    pointer-events: none;
  }

  @include media-breakpoint-only(xs) {
    width: calc(100vw - 40px); //$grid-gutter-width-xs
  }

  @include media-breakpoint-between(ssm, sm) {
    width: calc(100vw - 32px); // $grid-gutter-width
  }

  @include media-breakpoint-up(sm) {
    &:after {
      display: none;
    }
  }

  @include media-breakpoint-only(md) {
    width: 687px;
  }

  &:hover,
  &:active {
    color: $white;
  }

  padding-left: 24px;
  padding-right: 24px;

  @include media-breakpoint-up(sm) {
    // min-height: 359px;
    // padding-top: 60px;
    // padding-bottom: 60px;
    padding-left: 40px;
    padding-right: 40px;
  }

  @include media-breakpoint-up(lg) {
    // min-height: 355px;
    // padding-top: 56px;
    // padding-bottom: 42px;
    padding-left: 234px;
    padding-right: 100px;
  }

  @include media-breakpoint-up(xl) {
    // padding-left: 335px;
    padding-left: 300px; // ***
    padding-right: 60px;
  }


  h1,
  h2,
  h3 {
    color: $white;
    max-width: 232px;

    @include media-breakpoint-up(md) {
      max-width: 284px;
    }

    @include media-breakpoint-up(xl) {
      max-width: 416px;
    }
  }

  img {
    position: absolute;
    min-height: 100%;
    min-width: 100%;
    max-height: 100%;
    max-width: none;
    top: 0;
    right: 0;
    z-index: -1;
    transform: translate(214px, 0);

    @include media-breakpoint-up(sm) {
      transform: none;
      object-fit: cover; // не поддерживается IE, поэтому для разных размеров проставлены значения right
      height: 100%;
      right: -35.4%;
      width: auto;
    }

    @include media-breakpoint-up(md) {
      right: -26.8%;
    }

    @include media-breakpoint-up(lg) {
      right: -2.3%;
    }

    @include media-breakpoint-up(xl) {
      right: -4.6%;
    }
  }
}

// **** КНОПКИ СЛАЙДЕРА
.main-slider__arrows {
  transform: translate(-50%, -84%);
  margin-left: -20px;
  max-width: 93%;
  z-index: 700;

  @include media-breakpoint-down(md) {
    display: none;
  }

  @include media-breakpoint-up(xl) {
    transform: translate(-50%, -84%);
    margin-left: -20px;
  }

  .slider-arrows__next {
    button {
      background-color: rgba(0, 0, 0, 0.3);

      &:hover,
      &:active {
        background-color: rgba(0, 0, 0, 0.4);
      }
    }
  }

  .slider-arrows__prev {
    margin-left: -16px;
  }

  button {
    height: 54px;
    width: 54px;
    border-radius: 50%;

    @include animateBgcolor;

    &:after,
    &:before {
      height: 11px;
      left: 23px;
      border-radius: 0;
      // background-color: #535353;
    }

    &:after {
      bottom: 19px;
    }

    &:before {
      top: 19px;
    }
  }
}