// offer-big


.offer-big {
  overflow: hidden;

  position: relative;

  @include media-breakpoint-only(md) {
    padding-right: 40%;
  }

  p {
    max-width: 477px;

    &:not(.offer-big__price) {
      margin-top: 16px;
      margin-bottom: 24px;

      @include media-breakpoint-up(md) {
        margin-top: em(36px, 24px);
      }
    }
  }

  &:before {
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    background-color: $white;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  @include media-breakpoint-up(lg) {
    padding-left: 44px;
    padding-bottom: 55px;
    padding-top: 57px;
  }

  h3 {
    margin-top: 0;
    margin-bottom: 0;

    @include media-breakpoint-only(lg) {
      font-size: 24px - 4px;
    }
  }

  h4 {
    &:not(.icon-link__title) {
      margin-top: em(40px, 18px);
      margin-bottom: em(25px, 18px);
    }
  }

  ul,
  ol {
    margin-top: 0;
    margin-bottom: 0;

    color: $offer-small-text-color;
    max-width: $offer-text-max-width;

    li {
      margin-top: 0;
      margin-bottom: 0;

      &:first-of-type {
        margin-top: 0;
      }

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  &__price {
    margin-top: 16px;
    margin-bottom: 24px;

    @include media-breakpoint-up(md) {
      font-size: 24px;
      line-height: em(34px, 24px);
      margin-top: em(26px, 24px);
      margin-bottom: em(30px, 24px);
    }

    @include media-breakpoint-only(lg) {
      font-size: 24px - 4px;
    }

    @include media-breakpoint-down(sm) {
      color: $offer-price-color;
      font-weight: 500;
      font-size: 16px;
    }
  }

  a {
    margin-top: 30px;

    @include media-breakpoint-up(lg) {
      margin-top: 40px;
    }
  }


  &.multisignature {
    background: url(images/multisignature-bg.svg) top 30% right 0 no-repeat;
    background-size: 60% auto;

    @include media-breakpoint-up(md) {
      background:
        url(images/multisignature-bg.svg) top 0 right 0 no-repeat;
      background-size: 55% 100%;
      background-position: top 0 right 0;
    }

    @include media-breakpoint-up(lg) {
      background-size: 55% 100%;
      background-position: top 0 right 0;
    }
  }

  &.special-offer-government {
    background: url(images/graph-translucent-bg.svg) bottom 0 right 0 no-repeat;
    background-size: 100% auto;

    @include media-breakpoint-up(md) {
      background-size: 55% auto;
    }
  }

  &.reports {
    background:
      // url(images/offer-emblem.svg) top 0 left 0 no-repeat,
      url(images/offer-lines.svg) top 0 right 0 no-repeat;

    background-size:
      // 200px auto,
      auto 101%;

    @include media-breakpoint-up(md) {
      background: url(images/offer-lines.svg) no-repeat;

      background-size: auto 101%;

      background-position: bottom -2px right 0;
    }

    @include media-breakpoint-up(lg) {
      background: url(images/offer-emblem.svg) no-repeat,
        url(images/offer-lines.svg) no-repeat;

      background-size:
        200px auto,
        auto 101%;

      background-position:
        right 30% top -5px,
        bottom -2px right 0;
    }

    @include media-breakpoint-up(xl) {
      background-size: 315px 337px, 23% 100%;
      background-position:
        right 30% top -30px,
        bottom -2px right 0;
    }

    h3 {
      @include media-breakpoint-up(lg) {
        max-width: 290px;
      }
    }
  }

  &.state_portals-reports {
    background:
      // url(images/offer-emblem.svg) top 0 left 0 no-repeat,
      url(images/state_portals-reports.svg) bottom 40px right -125px no-repeat;

    background-size:
      // 200px auto,
      auto 73%;

    @include media-breakpoint-up(md) {
      background: url(images/state_portals-reports.svg) no-repeat;

      background-size: auto 64%;

      background-position: bottom 45px right 0
    }

    @include media-breakpoint-up(lg) {
      background: url(images/state_portals-reports.svg) no-repeat;

      background-size: auto 77%;

      background-position: bottom 31px right 0
    }

    @include media-breakpoint-up(xl) {
      background-size: auto 414px;
      background-position: bottom 31px right 0;
    }

    h3 {
      @include media-breakpoint-up(lg) {
        max-width: 360px;
      }
    }
  }
}

.offer-big__img {
  position: absolute;

  @include media-breakpoint-down(sm) {
    display: none;
  }

  &.imac-bg {
    bottom: 0;
    width: 35%;
    right: 5%;

    @include media-breakpoint-up(lg) {
      width: 35%;
    }

    @include media-breakpoint-up(xl) {
      width: 40%;
    }
  }
}

.flash-cards-bg {
  right: 6%;
  top: 9%;

  @include media-breakpoint-down(lg) {
    right: 0;
    top: 7%;
    width: 44%;
  }

  @include media-breakpoint-down(md) {
    right: 0;
    top: 23%;
    width: 40%;
  }
}

.state_portals-comp {
  right: 0;
  bottom: 0;
  width: 60%;
}

.offer-reports-comp-bg {
  right: 0;
  top: 25%;

  @include media-breakpoint-down(lg) {
    width: 40%;
  }

  @include media-breakpoint-down(md) {
    top: 30%;
  }
}