// contacts-support

.contacts-support {
  padding-bottom: 0px;
}

.contacts-support__title {
  @include media-breakpoint-up(sm) {
    margin-bottom: 20px;
  }
}
.contacts-support__list{
  flex-wrap: nowrap;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 0;
  margin-bottom: 0;
  @include media-breakpoint-down(md) {
    flex-wrap: wrap;
  }
}

.contacts-support__item + .contacts-support__item{
  margin-left: 32px;
  @include media-breakpoint-down(md) {
    margin-left: 0px;
  }
}
@include media-breakpoint-down(sm) {
  .contacts-support__list{
    margin-top: 0;
  }
}
@include media-breakpoint-down(md) {
  .contacts-support__list{
    padding-bottom: 0;
    margin-bottom: 0;
  }
  .contacts-support{
    padding-bottom: 0;
  }
}
@include media-breakpoint-down(md) {
  .tech-support__main-banner{
    .main-banner__content p {
      max-width: 290px;
    }
  }
}
.contacts-support__item:last-child{
  margin-bottom: 0;
}
.contacts-support__item {
  width: 100%;
  margin-bottom: 30px;
  background: $white;
  border: 1px solid $border-color;
  box-sizing: border-box;
  border-radius: $border-radius;
  display: flex;
  padding-left: 24px;
  padding-right: 24px;
  height: 120px;
  @include media-breakpoint-down(ssm) {
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 15px;
    height: auto;
  }
  .btn-skelet{
    height: 56px;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    align-items: center;
    padding-top: 0;
    padding-bottom: 0;
    @include media-breakpoint-down(lg) {
      height: 41px;
      font-size: 11px;
    }
    @include media-breakpoint-down(ssm) {
      margin-right: auto;
      margin-top: 25px;
      height: 50px;
      font-size: 14px;
      width: 100%;
      padding-left: 0;
      justify-content: center;
      padding-right: 0;
    }
  }
  

  .icon-link {
    min-height: 60px;
    margin-top: auto;
    margin-bottom: auto;
    // padding-left: 72px;
  }

  .icon-link__icon-wp {
    height: 60px;
  }

  .icon-link__phone {
    left: -14px;
  }

  .icon-link__mail {
    left: -10px;
  }

  @include media-breakpoint-up(sm) {
    .icon-link__text {
      font-size: em(18px, 16px);
    }

    .icon-link {
      padding-left: 72px;
    }

  }
}
.teacher-block + .contacts-support{
  h2{
    margin-top: 0;
  }
  margin-top: 70px;
  margin-bottom: 40px;
  @include media-breakpoint-down(lg) {
    margin-top: 60px;
    margin-bottom: 30px;
  }
  @include media-breakpoint-down(md) {
    margin-top: 50px;
    margin-bottom: 50px;
  }

}