.success {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  // font-weight: 500;
  text-align: center;

  @include media-breakpoint-up(sm) {
    padding-bottom: 13px;
  }
}

.modal__illustration-wp {
  margin: 0px auto 60px auto;
  width: 190px;
  height: 135px;
  transform: translateX(5px);

  @include media-breakpoint-up(sm) {
    margin: 29px auto 126px auto;
    transform: translateX(2px);
    width: 290px;
    height: 200px;
  }

}

.modal__illustration {
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
}

.success__text {
  max-width: 397px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 23px;

  @include media-breakpoint-up(sm) {
    margin-top: 14px;
  }
}