// verification-form


.verification-form {

  @include media-breakpoint-up(sm) {
    display: flex;
    align-items: flex-start;
  }
}

.verification-form__input {
  width: 100%;

  @include media-breakpoint-down(ssm) {
    margin-bottom: 1em;
  }


  @include media-breakpoint-up(sm) {
    width: 257px;
    margin-right: 30px;
  }

  &>.input__fluid {
    width: 100%;
  }
}


.verification-form__btn {
  width: 100%;


  @include media-breakpoint-up(sm) {
    width: auto;
    margin-top: 12px;
    height: 56px;
  }
}