//textarea

.textarea {
  // height: 137px;
}


.textarea__fluid {
  height: 137px;
  padding: em(13px, 16px) em(16px, 16px); // *** 13px 16px
  background-color: $white;
  border: 1px solid $input-border;
  border-radius: 6px;
  font-size: 1em;
  line-height: em(29px, 16px);

  @include animateBorder;

  &:hover {
    border-color: $input-border-hover;
  }

  &:focus,
  &:not([value=""]) {
    &+.input__placeholder {
      transform-origin: 0 0;
      transform: translate(-1px, -5px) scale(0.88);
    }
  }

  &.error {
    border-color: $input-error;

    &+.input__placeholder {
      color: $input-error;
    }

    &~.input__error {
      display: block;
    }
  }

  &.valid {
    &~.input__error {
      display: none;
    }
  }
}