// arrow-up

.arrow-up {
  position: fixed;
  bottom: 30px;
  right: 0;

  pointer-events: none;

  @include media-breakpoint-only(md) {
    right: (calc((100vw - 707px) / 2));
  }

  @include media-breakpoint-only(lg) {
    right: (calc((100% - 929px) / 2));
  }

  @include media-breakpoint-up(xl) {
    right: (calc((100% - 1182px) / 2));
  }
}

.arrow-up__btn {
  background: $white;
  box-shadow: 0px 4px 15px rgba(15, 17, 18, 0.08);
  height: 50px;
  width: 50px;
  border-radius: 50%;
  position: relative;

  cursor: pointer;
  pointer-events: painted;

  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0;
  margin-left: auto;

  @include animateBoxShadow;

  z-index: 703;

  &:hover,
  &:active {
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
  }

  @include media-breakpoint-up(sm) {
    height: 63px;
    width: 63px;
  }
}

.arrow-up__icon-wp {
  height: 11px;
  width: 20px;
  position: relative;
  display: block;
}

.svg-icon-arrow-up {
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}