// cost-calc

.cost-calc {
  margin-top: 50px;
  margin-bottom: 50px;

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
    margin-bottom: 70px;

  }
}

.cost-calc__header {
  background-color: #EDEDF1;
  border-radius: 10px 10px 0 0;
  padding: 24px;
  // position: relative;

  @include media-breakpoint-up(sm) {
    padding: 25px 43px;
  }
}

.cost-calc__title {
  max-width: 100%;
  margin: 0;

  // font-size: 18px;

  // @include media-breakpoint-up(xl) {
  //   font-size: 24px;
  // }

  @include media-breakpoint-up(sm) {
    display: flex;
    align-items: center;
  }

  &>span {
    font-weight: bold;
    font-size: 18px;
    line-height: em(25px, 18px);
    color: $main-dark-grey;
    display: block;
    position: relative;
    margin-bottom: 20px;

    @include media-breakpoint-up(sm) {
      margin-bottom: 0;
      line-height: 1em;
      font-size: 24px;
      // line-height: em(24px, 34px);
      // border-right: 2px solid $main-light-grey;
      padding-right: em(30px, 24px);
      margin-right: em(30px, 24px);
      // padding-top: .65em;
      // padding-bottom: .65em;

      &:after {
        content: '';
        display: block;
        height: 52px;
        width: 2px;
        background-color: $main-light-grey;
        position: absolute;
        right: 0;
        top: -.5em;
      }
    }
  }
}



// *** FORM

.cost-calc__form {
  background-color: $white;
  border-radius: 0 0 10px 10px;
}

.cost-calc__body {
  padding: 40px 20px;

  @include media-breakpoint-up(sm) {
    // padding: 40px 43px 38px 43px;
    padding: 40px 43px 50px 43px;
  }
}

.cost-calc__footer {
  border-top: 1px solid $main-light-grey;
  padding: 30px 24px 40px 24px;

  &>.btn-primary {
    width: 100%;

    @include media-breakpoint-up(sm) {
      width: auto;
    }
  }

  @include media-breakpoint-up(sm) {
    padding: 30px 43px 34px 43px;

  }
}

// *** INPUTS

.cost-calc__inputs {


  @include media-breakpoint-up(md) {
    display: flex;
    flex-wrap: wrap;
  }

  // @include media-breakpoint-up(xl) {
  //   margin-bottom: 10px;
  //   // margin-bottom: 6px;

  //   &:last-of-type {
  //     margin-bottom: 0;
  //   }
  // }

  &>.input {
    &>.input__fluid {
      width: 100%;
    }

    // &:last-of-type {
    // margin-bottom: 0;
    // }

  }

  &:last-of-type {
    &>.input:last-child {
      margin-bottom: 0;
    }
  }

  &>div {
    &:last-of-type {
      &>.region {
        &:last-of-type {
          &>.region__main-checkbox {
            margin-bottom: 0;
          }
        }
      }
    }

    &>.region {
      &:last-of-type {
        &>.region__main-checkbox {
          @include media-breakpoint-up(lg) {
            margin-bottom: 0;
          }
        }
      }
    }
  }

  &--1 {

    &>.input,
    &>div {
      width: 100%;
      display: block;

      &>.textarea__fluid {
        width: 100%;
        max-width: 100%;
      }
    }

    &>div>.input {
      width: 100%;
      max-width: 100%;

      &>.input__fluid {
        width: 100%;
        max-width: 100%;
      }
    }
  }


  // в блоке 2 input
  &--2 {

    &>.input,
    &>div {
      width: 100%;
      display: block;

      @include media-breakpoint-up(lg) {
        margin-right: 20px;
        // margin-right: 30px;
        width: calc((100% / 2) - 10px);
        // width: calc((100% / 2) - 15px);

        &:last-of-type {
          margin-right: 0;
        }
      }
    }

    &>div>.input {
      width: 100%;
      max-width: 100%;

      &>.input__fluid {
        width: 100%;
        max-width: 100%;
      }
    }
  }


  // в блоке 3 input
  &--3 {

    &>.input,
    &>div {
      width: 100%;
      display: block;

      @include media-breakpoint-up(lg) {
        width: calc((100% / 2) - 10px);
        // width: calc((100% / 2) - 15px);
        margin-right: 20px;
        // margin-right: 30px;

        &:nth-of-type(2) {
          margin-right: 0;
        }

        &:last-of-type {
          margin-right: 0;
        }
      }

      @include media-breakpoint-up(xl) {
        &:first-of-type {
          width: 35.81%;
          // width: 35.6%;
        }

        &:nth-of-type(2) {
          width: 20.96%;
          margin-right: 20px;
          // margin-right: 30px;
        }

        &:last-of-type {
          width: 39.47%;
          // width: 37.8%;
        }
      }
    }
  }
}


// *** 


.cost-calc__input-text {
  margin-top: 1em;
  color: $main-dark-grey;

  @include media-breakpoint-down(md) {
    margin-bottom: 0;

    small {
      margin-bottom: .3em;
    }
  }

  @include media-breakpoint-up(lg) {
    margin-top: .25em;
  }
}

.cost-calc {

  h4 {
    max-width: 100%;
    margin-top: 2.5em;
    margin-bottom: 1.4em;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0.6em;
      margin-top: .9em;
    }
  }
}

.cost-calc__body {
  &>h4:first-child {
    margin-top: .7em;

    @include media-breakpoint-up(md) {
      margin-top: 0;
    }

    @include media-breakpoint-up(lg) {
      margin-bottom: .8em;
    }
  }
}



.cost-calc__inner-flex {

  &>div {
    margin-bottom: .5em;

    &:last-of-type {
      margin-bottom: 0;
    }

    @include media-breakpoint-up(sm) {
      margin-bottom: 0;
    }
  }

  @include media-breakpoint-up(sm) {
    display: flex;
    flex-wrap: wrap;
  }

  &>.checkbox-wp {
    margin-right: 1.5em;

    &:last-of-type {
      margin-right: 0;
    }
  }
}