// offer

.offer-wp {
  margin-bottom: 30px;

  @include media-breakpoint-up(sm) {
    margin-bottom: 32px;
  }
}

.offer {
  background-color: $white;
  border: 1px solid $border-color;
  border-radius: $border-radius;
  padding: 30px 24px 30px 24px;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  @include media-breakpoint-up(lg) {
    padding: 30px 30px 30px 24px;
    // height: 346px;
  }

  @include media-breakpoint-up(xl) {
    padding-right: 78px;
  }
}

.offer__title {
  @include h4-style();
  margin-top: 0;
  margin-bottom: 0;
  max-width: 100%;


  @include media-breakpoint-down(sm) {
    line-height: em(22px, 16px);
  }

  @include media-breakpoint-only(lg) {
    font-size: 16px;
  }

  @include media-breakpoint-only(xl) {
    overflow: visible !important; // иначе нижняя часть текста срезается
  }
}

.offer__price {
  margin-bottom: 0;
  line-height: em(22px, 16px);
  margin-top: 16px;

  @include media-breakpoint-down(sm) {
    color: $offer-price-color;
    font-weight: 500;
    font-size: 16px;
    margin-top: 13px;
  }

  @include media-breakpoint-up(md) {
    margin-top: 10px;
  }

  &>span {
    margin: 0;
    display: inline-block;
    font-size: 1em;
    padding-right: em(16px, 22px);
    color: $offer-price-color;
    font-weight: 500;
  }
}

.offer__text {
  margin-top: 24px;
  margin-bottom: 28px;

  @include media-breakpoint-down(sm) {
    line-height: 20px;
    margin-bottom: 33px;

    &>small {
      line-height: 20px;
    }
  }

  @include media-breakpoint-up(md) {
    margin-top: 20px;
  }
}

.offer__small-text {
  font-size: 14px;
  color: $offer-small-text-color;
  margin-bottom: 0;
  line-height: em(20px, 14px);
  max-width: $offer-text-max-width;

  @include media-breakpoint-only(md) {
    font-size: 16px;
  }

  @include media-breakpoint-only(lg) {
    font-size: 14px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 16px;
  }
}

.offer__link {
  margin-top: auto;
  margin-bottom: 0;
  text-align: center;
  width: 100%;

  @include media-breakpoint-up(sm) {
    width: auto;
  }

  @include media-breakpoint-only(lg) {
    font-size: 14px;
  }
}