// partner-verification
.partner-verification {

  margin-top: 50px;
  margin-bottom: 50px;

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
    // margin-bottom: 70px;
    margin-bottom: 58px;
  }



  h1 {
    @include h2-style;
  }
}


.partner-verification__result {
  display: none;
  padding-top: 8px;
  margin-bottom: (70px - 58px);

  h3 {
    margin-bottom: 0.7em;
  }

  .btn-primary {
    width: 100%;


    @include media-breakpoint-up(sm) {
      width: auto;
    }
  }
}