.block-request-consultation{
    margin-top: 70px;
    padding-top: 70px;
    padding-bottom: 70px;
    background-color: $white;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    @include media-breakpoint-down(ssm) {
        padding-bottom: 40px;
        padding-top: 40px;
    }
    @include media-breakpoint-down(lg) {
        margin-top: 60px;
    }
    @include media-breakpoint-down(md) {
        margin-top: 50px;
    }
    @include media-breakpoint-up(md) {
      background-image: url('images/banner-pattern.svg');
      background-repeat: repeat-y;
      background-position: center right;
      background-size: 25%;
    }
  
    @include media-breakpoint-up(md) {
      border-color: $main-light-grey;
    }
  
    @include media-breakpoint-up(lg) {
      background-size: 250px;
    }
    .container{
        padding-left: 15px;
        padding-right: 15px;
    }
}
.consultation-top{
    .consultation__title{
        margin-top: 0;
        max-width: none;
    }
}
.consultation-form{
    max-width: 862px;
    display: flex;
    flex-wrap: wrap;
    .input{
        flex: 1 0 auto;
        max-width: none;
        margin-bottom: 0;
        input{
            width: 100%;
        }
        + .input{
            margin-left: 30px;
        }
        &__error{
            padding-top: 6px;
        }
    }
    .select-container{
        flex-basis: 100%;
        margin-top: 33px;
        position: relative;
    }
    .textarea-container{
        flex-basis: 100%;
        position: relative;
        margin-bottom: 23px;
        .input__error{
            padding-top: 0;
        }
        .input__placeholder{
            top: 10px;
        }
    }
    .input-name{

    }
    .input-email{

    }
    .input-question{
        width: 100%;
        margin-top: 16px;
        height: 137px;
        padding-left: 16px;
        padding-right: 8px;
        padding-top: 13px;
        &.error {
          border-color: $input-error;

          &+.input__placeholder {
            color: $input-error;
          }
          &~.input__error {
            display: block;
          }
        }
      
        &.valid {
          &~.input__error {
            display: none;
          }
        }
    }
    .input-question:focus + .input__placeholder{
        transform-origin: 0 0;
        top: 15px;
        transform: translate(-1px, -5px) scale(0.88);
    }
    .input__placeholder--acrive + .input__placeholder{
        transform-origin: 0 0;
        top: 15px;
        transform: translate(-1px, -5px) scale(0.88);
    }
    &__select{
        width: 100%;
        padding-left: 16px;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        background: url('images/consultation-form-arrow.svg');
        background-repeat: no-repeat;
        background-size: 16px 17px;
        background-position: calc(100% - 20px);
        &+.input__placeholder {
            transform-origin: 0 0;
            transform: translate(-1px, -5px) scale(0.88);
        }
    }
    &__agreement{
        font-family: Roboto;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 180%;
        color: $main-dark-grey;
    }
    .link-bottom{
        margin-left: 30px;
    }
    @include media-breakpoint-down(ssm) {
        .input{
            min-width: 100%;
            input{
            }
            + .input{
                margin-left: 0px;
            }
        }
        .consultation-form__submit{
            width: 100%;
        }
        .link-bottom{
            max-width: none;
            width: 100%;
            margin-left: 0;
            margin-top: 23px;
        }
    }
}
.form-bottom{
    display: flex;
    @include media-breakpoint-down(ssm) {
        flex-direction: column;
    }
}
.select__subject{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    color: #192630;
    height: 42px;
}
.consultation-form__input{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 180%;
    height: 56px;
    color: $main-dark-grey;
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
}
.consultation-form__textarea{
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
}
.consultation-form__select{
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
    height: 56px;
}
.consultation-form__submit{
    max-height: 56px;
}




.consultation-form{
    *,
*:before,
*:after{
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.select {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    height: 56px;
    /* Light grey */
    & + .input__placeholder{
        top: -27px;
        z-index: 10;
    }
}

.select-wrap {
	max-width: 700px;
	width: 100%;
	margin: 20px auto;
}

.select-wrap select {
	margin: 20px;
}

.select__gap {
	background-color: $white;
	cursor: pointer;
    position: relative;
    height: 100%;

    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 180%;
    display: flex;
    align-items: center;
    width: 100%;
    padding-left: 16px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    border-radius: 6px;
    z-index: 8;
}

.select__list {
    background: $white;
    background-color: $white;
    position: absolute;
    z-index: 5;
    width: 100%;
    padding-top: 15px;
    margin-top: -10px;

    border: 1px solid $main-light-grey;
    box-sizing: border-box;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
}

.select__list.on {
	display: block;
}

.select__item span {
    display: block;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 15px;
	cursor: pointer;
    color: #333;
}

.select__item:nth-child(2n){
    background-color: #F3F4F5;
}
.select__item:hover {
    background-color: $light-bg-color;
}
.select__gap:after {
	content: '';
	display: block;
	position: absolute;
    
    background-image: url('images/consultation-form-arrow.svg');
    background-repeat: no-repeat;
    background-size: 16px 17px;
    width: 16px;
    height: 17px;
    right: 20px;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;
	-webkit-transition: all .27s ease-in-out;
	     -o-transition: all .27s ease-in-out;
	        transition: all .27s ease-in-out;

	-webkit-transform: rotate(0deg);
	    -ms-transform: rotate(0deg);
	     -o-transform: rotate(0deg);
	        transform: rotate(0deg);
}

.on.select__gap:after {
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	     -o-transform: rotate(180deg);
	        transform: rotate(180deg);
}
}
.bottom-text{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 180%;
    margin: 0;
    color: $main-dark-grey;
}