// licenses-slider

.licenses-slider {
  padding-top: 108px;  

  @include media-breakpoint-up(sm) {
    padding-top: 110px;
  }

  @include media-breakpoint-up(lg) {
    padding-top: 60px;
  }
}

.licenses-slider__list {
  margin-top: 0;
  margin-bottom: 0;

  margin-left: auto;
  margin-right: auto;

  @include media-breakpoint-up(lg) {
    width: 640px;

    .slick-slide {
      margin-left: auto;
      margin-right: auto;

      display: -webkit-flex; // Safari  
      -webkit-justify-content: center; // safari 
      display: -ms-flexbox;
      display: flex;
      justify-content: center;

      &+div {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        -ms-flex: 1;
        flex: 1;
      }
    }
  }
}


.licenses-slider__img {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  width: 100%;
  flex-shrink: 0;
  display: block;
}

.licenses-slider__slide {
  pointer-events: auto;
  width: 280px;

  @include media-breakpoint-up(sm) {
    width: 568px;
  }

  @include media-breakpoint-up(lg) {
    width: 586px;

    margin-left: auto;
    margin-right: auto;
  }

  @include media-breakpoint-up(xl) {
    width: 640px;
  }
}

.licenses-slider__item {
  padding-left: 5px;
  padding-right: 5px;

  @include media-breakpoint-up(sm) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @include media-breakpoint-up(lg) {
    padding-left: 0px;
    padding-right: 0px;
  }
}


// ********** СТРЕЛКИ
.licenses-slider__arrows {
  display: none;
  margin-bottom: 0;
  margin-top: 60px;


  @include media-breakpoint-up(lg) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // max-width: 740px;
    max-width: 820px;
    height: 100%;
  }

  @include media-breakpoint-up(xl) {
    // max-width: 790px;
    max-width: 874px;
  }

  button {
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    pointer-events: none;

    &:before,
    &:after {
      background-color: $white;
      height: 21px;
      width: 4px;
      border-radius: 0;
    }
  }

  li {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 54px;
    padding-right: 54px;
    pointer-events: auto;
    cursor: pointer;

    &:active {
      button {

        &:after,
        &:before {
          background-color: $btn-slider-arrow-bg-hover;
        }
      }
    }

    @include media-breakpoint-up(sm) {

      &:hover {
        button {

          &:after,
          &:before {
            background-color: $btn-slider-arrow-bg-hover;
          }
        }
      }
    }
  }
}