// main-banner

.main-banner {
  position: relative;
  overflow: hidden;
  margin-bottom: 50px;

  //* вместо bg color
  &:before {
    content: '';
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $white;
    z-index: -1;
  }

  background-image: url('images/bg_pattern.svg');
  background-repeat: repeat-y;
  background-position: center right 275%;
  background-size: 135% auto;

  @include media-breakpoint-only(lg) {
    background-position: center right 195%;
    background-size: 165% auto;
  }

  @include media-breakpoint-only(md) {
    background-position: center right 145%;
    background-size: 240% auto;
  }

  @include media-breakpoint-only(sm) {
    background-position: center right 132%;
    background-size: 270% auto;
  }

  @include media-breakpoint-only(ssm) {
    background-position: center right 120%;
    background-size: 360% auto;
  }

  @include media-breakpoint-down(xs) {
    background-position: center right 115%;
    background-size: 420% auto;
  }
}

.main-banner {
  .input__error {
    color: $input-error;
  }
  form {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
    width: 100%;

    @include media-breakpoint-up(md) {
      max-width: 450px;
    }

    .input {
      width: 100%;
      max-width: 100%;
      margin-bottom: 4px;

      @include media-breakpoint-up(sm) {
        max-width: 373px;
      }

      input {
        width: 100%;
        max-width: 100%;
      }
    }

    button {
      margin-top: 18px;
      margin-bottom: 10px;

      @include media-breakpoint-up(sm) {
        align-self: flex-start;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    p {
      // margin-bottom: 0;
      max-width: 100%;

      @include media-breakpoint-up(sm) {
        max-width: 373px;
      }

      @include media-breakpoint-up(lg) {
        max-width: 100%;
      }
    }

    span {
      color: $main-dark-grey;
      margin-top: 0;
      margin-bottom: 0;

      a {
        font-size: inherit;
      }
    }
  }
}

// банер со сплошным фоном
.main-banner--solid-color {
  background-image: none;
  // background-color: $banner-solid-color-bg;

  &:before {
    background-color: $banner-solid-color-bg;
  }
}

.main-banner__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;

  @include media-breakpoint-up(md) {
    justify-content: center;
  }

  @include media-breakpoint-up(xl) {
    min-height: 525px;
    // padding-top: 95px;
    // padding-bottom: 107px;
    // height: 525px; // (!) если раскомментировать, то часть заголовка/текста будет срезаться
    padding-top: 70px;
    padding-bottom: 70px;
  }

  h1 {
    margin-bottom: 12px;
    max-width: 380px;

    @include media-breakpoint-up(md) {
      max-width: 390px;
    }

    @include media-breakpoint-up(xl) {
      margin-bottom: 9px;
      max-width: 548px;
      // max-width: 500px;
    }
  }

  p {
    overflow: hidden !important;
    line-height: em(25px, 14px);
    margin-bottom: 40px;

    &>a {
      color: $main-blue;
      font-size: 1em;

      &:hover,
      &:active {
        color: $link-color-hover;
      }
    }
  }

  b {
    font-size: 16px;
    margin-top: em(4px, 16px);

    margin-bottom: 24px;
    line-height: em(34px, 24px);
    font-weight: normal;

    @include media-breakpoint-up(sm) {
      font-size: em(24px, 16px);
      margin-bottom: 16px;
    }

    @include media-breakpoint-only(lg) {
      font-size: em(18px, 16px);
    }

    @include media-breakpoint-up(lg) {
      margin-bottom: em(10px, 24px);
      margin-top: em(16px, 24px);
    }

    span {
      margin-right: .6em;
    }
  }

  .buttons-wp {
    margin-top: 40px;
  }
}

