// Кнопки слайдера

.slider-arrows {

  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  pointer-events: none;

  button {
    pointer-events: visible;
    padding: 0;
    position: relative;
    height: 25px;
    width: 9px;

    &:after,
    &:before {
      content: '';
      display: block;
      width: 2px;
      background-color: $btn-slider-arrow-bg;
      height: 13px;
      position: absolute;
      left: 2px;
      transform-origin: 0.2em center;
      transition: 0.5s;
      border-radius: 2em;
    }

    &:after {
      bottom: 3px;
      transform: rotate(-40deg);
    }

    &:before {
      top: 3px;
      transform: rotate(40deg);
    }

    &:active {

      &:after,
      &:before {
        background-color: $btn-slider-arrow-bg-hover;
      }
    }

    @include media-breakpoint-up(sm) {

      &:hover {

        &:after,
        &:before {
          background-color: $btn-slider-arrow-bg-hover;
        }
      }
    }
  }

  .slider-arrows__next {
    button {
      transform: rotate(180deg);
    }
  }
}