// faq

.faq__list {
  border: 1px solid $faq-border-color;
  border-radius: $border-radius;
  margin-top: 40px;
  margin-bottom: 70px;
}

.faq__item {

  &:first-of-type {
    .faq__spoiler-title {
      border-top: 0 none;
    }
  }
}

.faq__spoiler {

  // Плавное появление спойлеров
  &.open {

    // .faq__spoiler-title~* {
    //   animation: sweep .5s ease-in-out;
    // }
    .faq__spoiler-title {
      .faq__spoiler-arrow {
        transform: rotate(90deg);
      }
    }
  }
}

.faq__spoiler-title {
  user-select: none;
  cursor: pointer;
  position: relative;
  border-top: 1px solid $faq-border-color;
  padding: em(28px, 18px) 30px em(26px, 18px) 50px;
  color: $faq-title-color;
  font-size: 16px;
  @include animateBgcolor;

  @include media-breakpoint-up(sm) {
    font-size: 18px;
    line-height: em(24px, 18px);
    padding: em(30px, 18px) 80px em(28px, 18px) 80px;
  }

  @include media-breakpoint-only(lg) {
    font-size: 16px;
  }

  &:hover,
  &:active {
    background-color: $faq-title-hover-bg;
  }
}

.faq__spoiler-title-text {
  max-width: 550px;
  display: block;
  font-size: 1em;
}

.faq__spoiler-arrow-wp {
  position: absolute;
  display: block;
  left: 14px;
  top: em(41px, 18px);
  transform: translateY(-50%);
  width: 24px;
  height: 24px;

  pointer-events: none;

  @include media-breakpoint-up(sm) {
    left: 26px;
  }
}

.faq__spoiler-arrow {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 23px;
  height: 23px;
  border-radius: 50%;
  border: 1px solid $btn-skelet-border-color;
  transition: transform .2s ease-out;

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;

    display: block;
    width: 7px;
    height: 7px;
    margin-left: -5px;
    margin-top: 1.5px;
    border-right: 1px solid $faq-arrow-color;
    border-top: 1px solid $faq-arrow-color;

    transform: rotate(45deg) translate(-50%, -50%);
  }
}

.faq__content {
  padding: 0 30px 30px 50px;

  display: none;

  @include media-breakpoint-up(sm) {
    padding: 0 80px 50px 80px;
  }

  &>:first-of-type {
    margin-top: 0;
  }

  &>:last-of-type {
    margin-bottom: 0;
  }

  &>p {
    margin-bottom: 1em;

    &>a {
      display: block;
      color: $faq-title-color;

      &:hover,
      &:active {
        color: $link-color-hover;
      }
    }
  }
}