// process

.process {
  padding-top: 34px;
}

.process__title {
  margin-top: 0;
}

.process__subtitle {
  margin-top: 10px;

  @include media-breakpoint-up(xl) {
    margin-bottom: 40px;
  }
}


.process__list {
  margin-top: 0;
  margin-bottom: 0;

  @include media-breakpoint-up(xl) {
    display: flex;
    justify-content: flex-start;
  }
}

.process__item {
  position: relative;
  margin-bottom: 16px;


  @include media-breakpoint-down(xs) {
    padding-left: 90px;
    display: flex;
    align-items: center;
    min-height: 64px;
  }

  &:after {
    content: '• • • • •';
    color: $main-light-grey;
    position: absolute;
    top: 15px;
    left: 120px;
    height: 6px;
    font-size: 27px;
    display: none;

    @include media-breakpoint-up(xl) {
      display: block;
    }
  }

  &:last-of-type {
    &:after {
      display: none;
    }
  }

  @include media-breakpoint-up(xl) {
    margin-right: 28px;
    padding-right: 0;

    flex: 0 0 18%;
    max-width: 18%;

    &:first-of-type {
      margin-right: 4px;
    }

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.process__number {
  width: 64px;
  height: 64px;
  background-color: $white;
  box-shadow: 0px 10px 30px rgba(92, 109, 153, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-weight: bold;
  font-size: 20px;
  line-height: em(28px, 20px);
  color: $main-blue;

  margin-bottom: 24px;
  user-select: none;


  @include media-breakpoint-down(xs) {
    position: absolute;
    left: 20px;
    top: 0;
    margin: 0;
    width: 50px;
    height: 50px;
    font-size: 16px;
  }
}

.process__text {
  @include media-breakpoint-down(xs) {
    margin-bottom: 0;
    margin-top: 0;

    &>small {
      margin-bottom: 0;
    }
  }
}