.radio__checkmark {
  border-radius: 50%;

  &:after {
    content: '';
    display: block;
    height: 9px;
    width: 9px;
    background-color: $checkbox-check-color;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    @include animateOpacity;
  }
}