// subscribe-newsletter

.subscribe-newsletter-wp {
  margin-top: 50px;
  margin-bottom: 50px;

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
    margin-bottom: 70px;
  }
}

.subscribe-newsletter__btn {

  @include media-breakpoint-down(sm) {
    padding-left: 20px;
    padding-right: 20px;
  }

}

.subscribe-newsletter--step {
  display: none; // ***********************************************
}

.subscribe-newsletter__img {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 44px;

  @include media-breakpoint-up(sm) {
    margin-bottom: 20px;
  }

  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
    position: absolute;
    bottom: -116px;
    right: 10%;
  }
}

.subscribe-newsletter {
  background-color: $white;
  border: 1px solid $border-color;
  border-radius: $border-radius;
  padding: 40px 44px;
  position: relative;

  overflow: hidden;

  @include media-breakpoint-up(sm) {
    padding: 60px;
  }

  @include media-breakpoint-up(md) {
    background-image: url('images/banner-pattern.svg');
    background-repeat: repeat-y;
    background-position: center right;
    background-size: 25%;
  }
}

.subscribe-newsletter__text {
  margin-top: 0;
  max-width: 100%;

  line-height: em(25px, 18px);

  @include media-breakpoint-up(sm) {
    line-height: em(34px, 24px);
  }
}

.subscribe-newsletter__btn {
  width: 100%;

  @include media-breakpoint-up(sm) {
    width: auto;
  }
}

