//checkbox



.checkbox {
  display: inline-block; // NEW
  display: block; // NEW
  position: relative;
  padding-left: 17px + 12px;
  cursor: pointer;
  user-select: none;

  font-size: $p-all-size-xs;
  line-height: em(25px, $p-all-size-xs);

  @include media-breakpoint-up(sm) {
    font-size: 16px;
    line-height: em(29px, 16px);
  }

  @include media-breakpoint-only(lg) {
    font-size: $p2-size-lg;
    line-height: $p2-line-height-lg;
  }
}

.checkbox__input {
  display: none;


  &:checked {
    &+.checkbox__checkmark {
      background-color: $checkbox-check-color;
      border-color: $checkbox-check-color;
    }

    &+.radio__checkmark {
      &:after {
        opacity: 1;
      }
    }

    &:not(.partial) {
      &+.checkbox__checkmark {
        &>.checkbox__icon {
          opacity: 1;
        }
      }
    }

    // частичный выбор
    &.partial {
      &+.checkbox__checkmark {

        &:after {
          content: '';
          position: absolute;
          height: 1px;
          width: 7px;
          display: block;
          background-color: $black;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}

.radio__checkmark,
.checkbox__checkmark {
  position: absolute;
  left: 0;
  display: block;
  // width: 17px;
  // height: 17px;
  width: 18px;
  height: 18px;
  border: 1px solid $input-border;
  box-sizing: border-box;

  @include animateButton;
  // top: .17em;

  // @include media-breakpoint-up(sm) {
  //   top: .35em;
  // }

  // @include media-breakpoint-only(lg) {
  //   top: .2em;
  // }
  // @include media-breakpoint-up(xl) {
  //   top: .3em;
  // }

  top: 45%;
  transform: translateY(-50%);
}

.checkbox__checkmark {
  border-radius: 3px;
}

.checkbox__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  width: 9px;
  height: 8px;
}