.partners-carousel{
    padding-left: 15px;
    padding-right: 15px;
    h2{
        margin-top: 0;
        margin-bottom: 0;
    }
    .carousel-content{
        display: flex;
        margin-top: 40px;
        align-items: center;
        margin-left: -16px;
        margin-right: -16px;
        @include media-breakpoint-down(md) {
            width: 90%;
            margin-left: auto;
            margin-right: auto;
            margin-top: 30px;
        }
    }
    .carousel-item{
        background: $white;
        border: 0.990937px solid $border-color;
        box-sizing: border-box;
        border-radius: 9.9px;
        width: 304px;
        height: 173px;
        padding-left: 30px;
        padding-right: 30px;
        display: flex !important;
        img{
            max-height: 85%;
            margin: auto;
        }
    }
    .slick-slide{
        margin-right: 16px;
        margin-left: 16px;
    }

}
.partners-arrow-left, .partners-arrow-right{
    position: absolute;
    cursor: pointer;
    stroke: $main-dark-grey;
    &:hover{
        stroke: $main-yellow;
    }
} 

.partners-arrow-right{
    -moz-transform: rotate(180deg); /* Для Firefox */
    -ms-transform: rotate(180deg); /* Для IE */
    -webkit-transform: rotate(180deg); /* Для Safari, Chrome, iOS */
    -o-transform: rotate(180deg); /* Для Opera */
    transform: rotate(180deg);
}
.container-arrow-right{
    height: 173px;
    min-width: 57px;
    position: absolute;
    right: -42px;
    cursor: pointer;
    @media (max-width: 600px) {
        min-width: 50px;
        right: -36px;
    }
    &:hover{
        svg{
            stroke: $main-yellow;
        }
    }
}
.container-arrow-left{
    height: 173px;
    min-width: 57px;
    position: absolute;
    left: -42px;
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    @media (max-width: 600px) {
        min-width: 50px;
        left: -36px;
    }
    &:hover{
        svg{
            stroke: $main-yellow;
        }
    }
}

.partners-arrow-right{
    margin-right: auto;
    height: 173px;
    right: 0;
    left: 0;
    margin-left: auto;
}


.partners-arrow-left{
    margin-right: auto;
    height: 173px;
    right: 0;
    left: 0;
    margin-left: auto;
}