// contacts

.contacts {
  padding-bottom: 26px;
}

.contacts__title {
  @include media-breakpoint-up(sm) {
    margin-bottom: 20px;
  }
}

.contacts__item {
  margin-bottom: 30px;

  .icon-link {
    min-height: 60px;
    // padding-left: 72px;
  }

  .icon-link__icon-wp {
    height: 60px;
  }

  .icon-link__phone {
    left: -14px;
  }

  .icon-link__mail {
    left: -10px;
  }

  @include media-breakpoint-up(sm) {
    .icon-link__text {
      font-size: em(18px, 16px);
    }

    .icon-link {
      padding-left: 72px;
    }

  }
}