// region
.region {
  position: relative;

  // border-radius: 6px; 

  // .checkbox__checkmark {
  //   z-index: 702;
  // }
}

.region__main-checkbox {
  background: $white;
  border: 1px solid $main-light-grey;
  border-radius: 6px;
  padding: 14px 30px 14px 16px;
  cursor: pointer;
  position: relative;
  margin-bottom: 24px;

  @include media-breakpoint-up(sm) {
    padding-right: 46px;
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    background-image: url(images/consultation-form-arrow.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: 11px;
    height: 6px;
    right: 16px;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;
    transition: all .27s ease-in-out;
    transform: rotate(0deg);

    @include media-breakpoint-up(sm) {
      width: 16px;
      height: 8px;
      right: 23px;
    }
  }

  &.show {
    z-index: 701;

    &:after {
      transform: rotate(180deg);
    }
  }
}

.region__content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  max-width: 100%;
  background-color: $white;
  border: 1px solid $main-light-grey;
  border-top: 0 none;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
  border-radius: 0 0 6px 6px;
  z-index: 700;
  padding: (6px + 4px) 16px;
  margin-bottom: 0;
  margin-top: -4px;

  max-height: 160px;
  overflow-y: auto;


  @include media-breakpoint-up(sm) {
    padding-left: 49px;
    max-height: 180px;
  }

  @include media-breakpoint-up(md) {
    top: calc(100% - 24px);
  }

  @include media-breakpoint-up(lg) {
    max-height: 155px;
  }

  @include media-breakpoint-up(xl) {
    max-height: 180px;
  }
}

.region__item {
  margin-top: .5em;
  margin-bottom: 1.2em;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.region__click-area {
  width: 40px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  z-index: 699;
}