// рекомендация в виде таблицы
.promo-wp {
  // max-width: 416px; /// ********************
  max-width: 100%;
  margin-bottom: 24px;

  @include media-breakpoint-up(md) {
    height: calc(100% - 24px);
  }
}

.promo {
  background-color: $white;
  border: 1px solid $promo-border;
  border-radius: $border-radius;
  // display: block; /// ********************

  display: flex;
  flex-direction: column;

  font-size: 14px;
  line-height: em(22px, 16px);

  // width: 416px; /// ********************
  width: 100%;
  max-width: 100%;
  height: 100%;

  td,
  th {
    display: block;
    padding: 0;
  }

  @include media-breakpoint-up(sm) {
    font-size: 16px;
  }

  &:not(.promo-logo) {
    padding: 25px 21px 27px 21px;
  }

  &.promo-logo {
    padding: 10px 21px 27px 21px;
  }

  // *** -- flex настройки
  .promo__body {
    @include media-breakpoint-up(md) {
      flex-grow: 1;
    }
  }

  .promo__footer {
    @include media-breakpoint-up(md) {
      flex-grow: 0;
    }
  }

  // *** END -- flex настройки


  // &:not(.promo-logo) {
  //   .promo__header {
  //     @include media-breakpoint-up(md) {
  //       // flex-basis: 220px; // обсудили с дизайнером, это лучше убрать
  //     }
  //   }
  // }
}



// *****************
// *** promo__header
.promo__header {
  display: block;
  border-bottom: 1px solid $promo-line;

  th {
    font-weight: normal;

    &:first-child {
      font-weight: 500;
      font-size: 16px;
      line-height: em(25px, 18px);

      @include media-breakpoint-up(sm) {
        font-size: 18px;
      }
    }
  }
}

.promo__header_text {
  padding-bottom: 28px;

  th {
    &:first-child {
      margin-bottom: 23px;
      margin-top: 4px;
    }
  }
}

.promo__header_img,
.promo__header_svg {
  th {
    &:first-child {
      // margin-bottom: 17px; // ****
      margin-bottom: 12px;
      // margin-top: 3px; // ****
    }
  }
}

.promo__img-wp,
.promo__svg-wp {
  // width: 163px; // ******
  width: 180px;
  // height: 50px; // ******
  height: 74px;
  display: block;

  display: flex;
  align-items: center;

  svg {
    height: 100%;
    width: 100%;
    max-height: 100%;
    max-width: 100%;
  }

  img {
    display: block;
    max-height: 100%;
    max-width: 100%;
  }
}


// *****************
// *** promo__body
.promo__body {
  display: block;
  padding: 8px 0 39px 0;

  td {
    &:first-child {
      color: $promo-color;
      margin-top: 29px;
      margin-bottom: 9px;
    }
  }
}


// *****************
// *** promo__footer
.promo__footer {
  display: block;
  border-top: 1px solid $promo-line;

  td {
    &:first-child {
      margin-top: 27px;
    }
  }

  tr {
    width: 100%;
    display: block;
  }

  .btn-primary {
    width: 100%;


    @include media-breakpoint-up(sm) {
      width: auto;
    }
  }
}