// quote-card

.quote-card {
  background-color: $main-white;
  border: 1px solid $border-color;
  border-radius: $border-radius;
  margin: 36px 0;
  position: relative;

  padding: 90px 30px 50px 30px;

  @include media-breakpoint-up(sm) {
    padding: 110px 45px 60px 45px;
  }

  @include media-breakpoint-up(lg) {
    padding: 50px 112px 60px 112px;
    margin: 52px 0 40px 0;
  }

  @include media-breakpoint-up(xl) {
    padding-right: 224px;
  }
}

.quote-card__icon {
  position: absolute;
  width: 40px;
  height: 32px;
  top: 30px;
  left: 30px;

  @include media-breakpoint-up(sm) {
    top: 45px;
    left: 45px;
  }

  @include media-breakpoint-up(md) {
    top: 51px;
    // left: 45px;
  }
}

.quote-card__title {
  margin-top: 0;

  @include media-breakpoint-up(lg) {
    margin-bottom: em(42px, 34px);
  }
}

.quote-card__text {
  @include media-breakpoint-up(lg) {
    margin-bottom: 1em;
  }
}

.quote-card__footer {
  position: relative;
  margin-top: 40px;

  @include media-breakpoint-up(ssm) {
    // padding-left: 102px;
    min-height: 78px;
    display: flex;
    align-items: center;
  }
}

.quote-card__img-wp {
  width: 78px;
  height: 78px;
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;


  @include media-breakpoint-up(ssm) {
    // position: absolute;
    // left: 0;
    // top: 0;
    margin-bottom: 0;
    margin-right: 24px;
  }
}

.quote-card__img {
  flex-shrink: 0;
}

.quote-card__author {
  display: block;
  font-style: normal;
  font-weight: 500;
  // padding-top: .8em;

  @include media-breakpoint-up(sm) {
    font-size: em(18px, 16px);
  }

  small {
    font-weight: normal;
    display: block;
    font-size: em(16px, 18px);
    color: $main-dark-grey;
    // margin-top: .3em;

    max-width: 540px;
  }

  @include media-breakpoint-up(ssm) {
    margin-top: 0;
    padding-top: 0;

    small {
      margin-top: 0;
    }
  }
}