// service

.services {
  padding-top: 8px;
}

.services__list {
  margin-top: 10px;
}



.services__item {
  margin-bottom: 30px;

  @include media-breakpoint-up(md) {
    margin-bottom: 40px;
  }
}

.service {
  background-color: $white;
  border: 1px solid $border-color;
  box-sizing: border-box;
  border-radius: $border-radius;
  display: block;
  padding: 30px 24px 28px 24px;
  height: 100%;

  @include media-breakpoint-up(sm) {
    &:hover {
      .service__svg {
        fill: #FBBF27;
      }
    }
  }
}

.service_card {
  display: block;
}

.service__round {
  @include animateOpacity;
}

.service__svg {
  height: 100%;
  max-height: 100%;
  width: 100%;
  max-width: 100%;
  fill: $white;
  transition: fill .4s ease;
}



.service__icon-wp {
  position: relative;
  height: 78px;
  width: 78px;

  margin-bottom: 30px;
}

.service__title {
  @include h4-style();
  margin-top: 0;
  margin-bottom: 24px;

  @include media-breakpoint-only(lg) {
    font-size: 16px;
  }
}

.service__text-wp {
  margin-bottom: 0;
  margin-top: 0;
  line-height: em(22px, 16px);
}

.service__text {
  font-size: 14px;
  color: $main-dark-grey;

  @include media-breakpoint-down(lg) {
    margin-bottom: 0;
  }



  @include media-breakpoint-only(md) {
    font-size: 16px;
  }

  @include media-breakpoint-only(lg) {
    font-size: 14px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 16px;
  }
}

.service__btn {
  margin-top: 28px;
  // padding-top: 16px;
  // padding-bottom: 16px;
  text-align: center;

  width: 100%;

  @include media-breakpoint-up(sm) {
    width: auto;
  }

  @include media-breakpoint-only(md) {
    font-size: 16px;
  }

  @include media-breakpoint-only(lg) {
    font-size: 14px;
  }

  &:hover,
  &:active {
    color: $main-blue;
  }
}

// *********************************************************************
