// *********************************************************************
// *********************************************************************

.service-card_big {
  @include media-breakpoint-up(md) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @include media-breakpoint-up(lg) {
    padding-right: 57px;
  }

}

.service__content_big {
  @include media-breakpoint-up(md) {
    order: -1;
  }
}

.service__icon-wp_big {

  @include media-breakpoint-up(md) {
    margin-bottom: 0;
    height: 100px;
    width: 124px;
  }
}

.service__text-wp_big {
  @include media-breakpoint-up(md) {
    max-width: 360px;
    margin-right: 10px;
  }
}


