// modal-state-platforms
.state-platforms {
  .checkbox-wp {
    margin-top: .3em;
  }

  .modal__text_small {
    @include media-breakpoint-up(xl) {
      max-width: 488px;
      margin-top: 20px;
    }
  }
}

.state-platforms__title {
  margin-bottom: 22px;

  @include media-breakpoint-down(xs) {
    max-width: 210px;
  }

  @include media-breakpoint-up(sm) {
    margin-bottom: 26px;
  }
}


.modal__price {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  margin-top: 0;

  font-size: 16px;
  margin-bottom: 21px;

  @include media-breakpoint-up(sm) {
    font-size: em(16px, 14px);
    margin-bottom: em(35px, 24px);
  }

  @include media-breakpoint-up(lg) {
    font-size: 20px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 24px;
  }

  &>span,
  &>del {
    display: inline-block;
    font-size: 1em;
  }

  &>span {
    margin: 0 20px 0 0;
  }
}

.modal__form {
  .state-platforms__text {

    @include media-breakpoint-down(sm) {
      order: 0;
      margin-top: .6em;
      // margin-bottom: 1.1em;
      margin-bottom: 0.9em;

      &+.modal__input {
        margin-top: 20px;
      }
    }

    @include media-breakpoint-up(sm) {
      margin-top: .7em;
      margin-bottom: 2em;
    }
  }
}