.one-time-services{
    margin-top: 70px;
    margin-bottom: 70px;
    @include media-breakpoint-down(lg) {
        margin-top: 60px;
        margin-bottom: 60px;
    }
    @include media-breakpoint-down(md) {
        margin-top: 50px;
        margin-bottom: 50px;
    }
    .services-content{
        @include media-breakpoint-up(md) {
            margin-left: -15px;
            margin-right: -15px;   
        }
        @include media-breakpoint-down(xs) {
            margin-left: -20px;
            margin-right: -20px;   
        }
    }
    .services__title{
        margin-top: 0;
        margin-bottom: 40px;
        max-width: 800px;
        @include media-breakpoint-down(ssm) {
            margin-bottom: 30px;
        }
    }
    .services__item{
        cursor: pointer;
        background-color: $white;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        padding: 24px 30px;
        margin-bottom: 0;
        transition-timing-function: ease;
        transition: transform .3s, box-shadow .3s;
        display: block;
        &:hover{
            @include media-breakpoint-up(lg) {
                transform: translateY(-10px);
                box-shadow: 0px 25px 34px -22px rgba(0, 0, 0, 0.25);
            }
        }
        + .services__item{
            margin-top: 20px;
        }
        h4{
            margin-top: 0;
            max-width: 850px;
            margin-bottom: 16px;
        }
        .offer__price{
            margin-top: 0;
            // span{
            //     @include media-breakpoint-up(sm) {
            //         color: $main-black;
            //     }
            // }
        }
        .item_bottom{
            margin-bottom: 0;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 140%;
            color: $main-dark-grey;
            margin-top: 29px;
        }
    }
}