// modal-licenses

.modal__content_licenses {
  padding: 0;
  background: none;
  border: none;
  width: 100%;
  border-radius: 0;
  left: 0;
  top: 0;
  margin-top: 0;
  // transform: none;
  pointer-events: none;
  max-height: 100%;

  @include media-breakpoint-up(lg) {
    // width: 750px;
    width: 820px;
  }

  @include media-breakpoint-up(xl) {
    width: 874px;
  }

  // ***************
  .modal__close {
    display: none;
  }

  // end modal__content_licenses
}
.modal-licenses-teacher{
  .licenses-slider__slide{
    margin-left: auto;
    margin-right: auto;
  }
}

.modal__content_licenses {
  &+.modal__close-screen {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;

    top: 20px;
    right: 50%;
    transform: none;
    width: 40px;
    height: 40px;
    margin-right: -20px;

    @include animateBgcolor;

    @include media-breakpoint-up(sm) {
      width: 50px;
      height: 50px;
      top: 30px;
      margin-right: -25px;

    }

    @include media-breakpoint-up(lg) {
      right: 20px;
      top: 20px;
      margin-right: 0;
    }

    .modal__close-icon {
      height: 20px;
      width: 20px;
      stroke: rgb(255, 255, 255);
      stroke-width: 2px;
    }

    &:hover,
    &:active {
      background-color: rgba(0, 0, 0, 0.4);
    }
  }
}