// }

.search-result__price-wp,
.search-result__content {
  margin-bottom: 20px;

  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
    padding-right: 1em;
  }
}

.search-result__price-wp,
.search-result__info {
  @include media-breakpoint-down(md) {
    width: 50%;
  }
}

.search-result__price-wp {

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  order: 2; // **************************************** ORDER *******************************************

  @include media-breakpoint-up(lg) {
    flex-basis: 15.8%;
    order: 1; // **************************************** ORDER *******************************************
  }
}

.search-result__content {

  order: 1; // **************************************** ORDER *******************************************

  @include media-breakpoint-up(lg) {
    flex-basis: 70.9%;
    order: 2; // **************************************** ORDER *******************************************
  }
}

.search-result__info {
  order: 3; // **************************************** ORDER *******************************************

  @include media-breakpoint-up(lg) {
    flex-basis: 13.3%;

  }

}

.search-result__info-inner {
  &:not(:last-of-type) {
    margin-bottom: em(20px, 16px);
  }
}

.search-result__price {
  @include h4-style();
  margin: 0;
  color: $main-black;
}

.search-result__number {
  color: $main-black;
}