// answer-card

.answer-card {
  padding: 30px 30px 30px 30px;

  display: block;

  @include media-breakpoint-up(sm) {
    padding: 30px 40px 40px 40px;

  }

  a {
    @include link-style-primary;
  }

  ul,
  ol {
    margin-top: 0;
    margin-bottom: 0;

    li {
      @include media-breakpoint-only(lg) {
        font-size: 16px;
        line-height: em(29px, 16px);
      }
    }
  }

  p:last-child {
    margin-bottom: 0;
  }

  p {
    margin-top: 1em;
    margin-bottom: 1em;

    @include media-breakpoint-only(lg) {
      font-size: 16px;
      line-height: em(29px, 16px);
    }
  }

  li {
    p {
      margin-top: 0.4em;
      margin-bottom: 0.4em;
    }
  }

  i {
    color: $link-color;
    line-height: 1.4em;
    display: block;
  }

  .answer-card__title {
    // @include h2-style;
    max-width: 526px;
    // margin-top: 20px;
    margin-top: em(20px, 34px);
    // margin-bottom: 22px;
    margin-bottom: em(22px, 34px);

    @include media-breakpoint-up(xl) {
      @include h2-style;
    }
  }
}


.answer-card__number {
  @include h3-style;
  color: $answer-card-number-color;
}

.answer-card__footer {
  margin-top: 30px;

  p {
    margin-top: 0;
  }

  @include media-breakpoint-up(md) {
    display: flex;
    align-items: center;
  }
}

.answer-card__btn {
  margin-bottom: 20px;

  width: 100%;

  @include media-breakpoint-up(sm) {
    width: auto;
  }

  @include media-breakpoint-up(md) {
    margin-bottom: 0;
    margin-right: 30px;
  }

  @include media-breakpoint-up(xl) {
    margin-right: 45px;
  }
}