// instructions

.instructions {
  padding-top: 0px;
  margin-top: 70px;

  .icon-link__icon-wp {
    height: 64px;
    width: 54px;
  }
  
  .icon-license {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
  }

  .icon-link {
    min-height: 64px;
    padding-left: 60px;
    @include media-breakpoint-down(ssm) {
      .icon-link__icon-wp {
        margin-bottom: 10px;
      }
    }
  }
}

.instructions__content {
  border: 1px solid $main-light-grey;
  box-sizing: border-box;
  border-radius: $border-radius;
  padding: 30px 20px 0px 20px;

  @include media-breakpoint-up(ssm) {
    padding: 50px 45px 29px 45px;
  }
}

.instructions__title {
  margin-top: 0;
}

.instructions__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 46px;
  margin-bottom: 0;
  @include media-breakpoint-down(sm) {
    margin-top: 30px;
  }
  @include media-breakpoint-down(md) {
    flex-wrap: wrap;
  }
}

.instructions__item {
  max-width: 100%;
  margin-bottom: 20px;
  width: 100%;
  margin-right: 5px;
  flex-basis: 24%;
  @include media-breakpoint-only(md) {
    flex-basis: 49% !important;
    .icon-link__text {
      font-size: $p2-size-md;
    }
  }
  @include media-breakpoint-only(lg) {
    .icon-link__text {
      font-size: $p2-size-lg;
    }
  }
  @include media-breakpoint-down(sm) {
    flex-basis: 100% !important;
  }

  .icon-link{
    align-items: flex-start;
    @include media-breakpoint-down(md) {
      align-items: center;
    }
  }
  .icon-link__text{
    padding-top: 6px;
    @include media-breakpoint-down(md) {
      padding-top: 0;
    }
  }
}
.instructions--two{
  .instructions__item:first-child{
    margin-right: 50px;
  }
}
.instructions--three{
  .instructions__item{
    flex-basis: 32%;
    .icon-link{
      max-width: 320px;
      @include media-breakpoint-down(md) {
        max-width: none;
      }
    }
  }
}