// links-block

.links-block {
  padding-top: 16px;
  padding-bottom: 10px; // ****** TEST

  @include media-breakpoint-up(md) {
    padding-top: 13px;
  }
}

.links-block-content {

  h2 {
    margin-top: 10px;
    margin-bottom: 0;
  }

  ul {
    @include media-breakpoint-up(md) {
      margin-bottom: 60px;
    }


    li {
      &:first-of-type {
        margin-top: 0;
      }

      &:before {
        top: .8em;
        left: .1em;
      }
    }
  }
}

.links-block__list {
  @include media-breakpoint-up(sm) {
    column-count: 2;
    column-gap: 30px;
  }

  @include media-breakpoint-up(md) {
    column-gap: 20px;
  }

  @include media-breakpoint-up(lg) {
    column-gap: 40px;
  }

  &.two {
    @include media-breakpoint-up(lg) {
      column-width: $links-block-item-width-2;
      max-width: 660px;

      .links-block__item {
        max-width: $links-block-item-width-2;
      }
    }
  }

  &.three {
    @include media-breakpoint-up(lg) {
      column-count: 3;
      column-width: $links-block-item-width-3;
      max-width: 990px;

      .links-block__item {
        max-width: $links-block-item-width-3;
      }
    }
  }

  &.four {
    @include media-breakpoint-up(md) {
      column-count: 3;
    }

    @include media-breakpoint-up(lg) {
      column-count: 4;
      column-width: $links-block-item-width;
      max-width: 890px;
    }
  }
}

.links-block__item {
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid; //  Makes effect only in Firefox */
  break-inside: avoid; //IE10+, Opera 11.1—12.1 

  &:first-of-type {
    margin-top: 0;
  }
}

.links-block__link {
  color: $links-block-item-color;
}

.epc-detail__links-block {
  @include media-breakpoint-up(lg) {
    padding-top: 32px;
  }
}

.tech-support__links-block {
  padding-top: 0;
  margin-top: 60px;
}

.epc-state_portals__links-block {

  @include media-breakpoint-up(lg) {
    margin-top: 76px;
  }
}

.tech-support__links-block {
  .links-block__list {
    max-width: none;
  }

  .links-block__item {
    max-width: none;
  }
}

.links-block__text {
  color: $main-dark-grey;
}