// modal-search
.modal__content {

  .modal-search {
    padding-top: 50px;
    padding-left: 0;
    padding-right: 0;


    @include media-breakpoint-up(sm) {
      padding-top: 70px;
      padding-bottom: 70px;
    }
  }
}

.modal-search {
  .input-search {
    margin: 0;
  }
}

