.modal-search__results {
  // padding-left: 16px;
  // padding-right: 16px;
  // margin-top: 40px;
  padding-top: 6px;

  h3 {
    padding-left: 16px;
    padding-right: 16px;
    margin-bottom: 0.5em;
    margin-top: 1.4em;


    &:first-child {
      margin-top: 34px;
    }
  }

}

.modal-search__no-results {
  padding-left: 16px;
  padding-right: 16px;

  p {
    margin-bottom: 1.1em;
  }
}



.modal-search__link {
  color: $main-black;

  display: block;

  padding-top: 0.47em;
  padding-bottom: 0.47em;
  padding-left: 16px;
  padding-right: 16px;

  border-radius: 3px;

  @include animateBgcolor;

  &:active {
    color: inherit;
    background-color: $light-bg-color;
  }

  @include media-breakpoint-up(sm) {
    display: flex;
    justify-content: space-between;

    &:hover,
    &:focus {
      background-color: $light-bg-color;
      color: inherit;
    }

  }
}


.modal-search__show-all {
  margin-left: 16px;
  margin-right: 16px;
  margin-top: 0.5em;

  width: calc(100% - 16px - 16px);

  @include media-breakpoint-up(sm) {
    width: auto;

  }
}




.modal-search__price {
  color: $main-blue;
  min-width: 160px;
  display: block;
  // padding-left: 16px;

  @include media-breakpoint-up(sm) {
    text-align: right;
  }
}

.modal-search__short-list {
  li {
    display: none;

    &:nth-of-type(1),
    &:nth-of-type(2),
    &:nth-of-type(3) {
      display: block;
    }
  }
}