.licenses-service {
    margin-bottom: 70px;
    @include media-breakpoint-down(lg) {
      margin-bottom: 60px;
    }
    @include media-breakpoint-down(md) {
      margin-bottom: 50px;
    }
    .icon-link__icon-wp {
      height: 64px;
      width: 54px;
    }
  
    .icon-license {
      width: 100%;
      height: 100%;
      max-width: 100%;
      max-height: 100%;
    }
  
    .icon-link {
      min-height: 64px;
  
      @include media-breakpoint-down(ssm) {
        .icon-link__icon-wp {
          margin-bottom: 10px;
        }
      }
    }
  }