//offer-img

.offers {

  @include media-breakpoint-up(lg) {
    padding-top: 27px;
  }
}

.offer-img {

  .offer__link {}
}


.offer-img__image-wp {
  background-color: #EDEDF1;
  border-radius: 10px 10px 0 0;

  height: 200px;
  overflow: hidden;
  display: block;
  // display: flex;
  // align-items: flex-end;
  // justify-content: center;
  position: relative;

  img {
    // flex-shrink: 0;

    position: absolute;
    // bottom: -40px;
    bottom: 0px;
    left: 50%;
    max-height: 95%;
    transform: translateX(-50%);

    @include media-breakpoint-up(sm) {
      max-height: none;
    }
  }

  &.eagle {
    img {
      bottom: -40px;

      @include media-breakpoint-up(sm) {
        bottom: -78px;
      }
    }
  }

  &.gavel {

    img {
      bottom: -40px;
      transform: translateX(-62%);
    }
  }

  &.notebook-img {
    img {
      bottom: 0;

      @include media-breakpoint-down(sm) {
        max-width: 150%;
      }
    }
  }

  &.site-img {
    img {
      bottom: 0;

      @include media-breakpoint-down(sm) {
        max-width: 130%;
      }
    }
  }
}


.offer-img__content {
  border-radius: 0 0 10px 10px;
  // height: auto;
  height: calc(100% - 200px);
}