// specify-city

.specify-city {
  position: absolute;
  padding-left: 20px;
  padding-right: 20px;
  height: 100vh;
  width: 100vw;

  top: 0;
  left: 0;
  z-index: 800;

  display: flex;
  align-items: center;
  justify-content: center;

  @include media-breakpoint-up(ssm) {}

  @include media-breakpoint-up(md) {
    height: auto;
    width: auto;
    // display: block;
    top: 88%;
    background-color: transparent;
    padding-left: 16px;
    padding-right: 16px;
    left: calc((100% - 707px - 10px) / 2);
  }

  @include media-breakpoint-up(lg) {
    left: calc((100% - 929px) / 2);
  }

  @include media-breakpoint-up(xl) {
    left: calc((100% - 1182px) / 2);
  }

  display: none;

  // &:not(.active) {
  //   display: none;

  //   &>.specify-city__content {
  //     box-shadow: 0px 10px 30px rgba(0, 0, 0, 0);
  //   }
  // }

  &.show {
    .specify-city__content {
      box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
    }
  }
}

.specify-city__overlay {
  background-color: rgba(15, 17, 18, 0.5);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;

  @include media-breakpoint-up(md) {
    display: none;
  }
}

.specify-city__content {
  position: relative;
  background-color: $white;
  border: 1px solid $border-color;
  box-sizing: border-box;
  // box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: $border-radius;

  padding: 30px 24px;

  @include animateBoxShadow;
  // transition: box-shadow 0.3s ease;

  @include media-breakpoint-down(sm) {

    &>button,
    &>a {
      width: 100%;
      font-size: 16px;
    }

    &>.specify-city__btn {
      margin-bottom: 24px;
    }
  }
}

.specify-city__title {
  margin-top: 0;
  font-size: 24px;
}

.specify-city__btn {
  margin-right: 16px;
}

.btn-progress {
  position: relative;
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;

    background-color: $main-black;
    opacity: 0.1;
    transition: width 5s ease;
  }

  &.in-progress {
    &:after {
      width: 100%;
    }
  }
}