// licenses

.licenses {
  padding-top: 14px;

  .icon-link__icon-wp {
    height: 64px;
    width: 54px;
  }

  .icon-license {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
  }

  .icon-link {
    min-height: 64px;

    @include media-breakpoint-down(ssm) {
      .icon-link__icon-wp {
        margin-bottom: 10px;
      }
    }
  }
}

.licenses__content {
  border: 1px solid $main-light-grey;
  box-sizing: border-box;
  border-radius: $border-radius;
  padding: 30px 20px 0px 20px;

  @include media-breakpoint-up(ssm) {
    padding: 50px 45px 29px 45px;
  }
}

.licenses__title {
  @include h2-style;
  margin-top: 0;
}

.licenses__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 46px;
  margin-bottom: 0;

  @include media-breakpoint-only(lg) {
    flex-wrap: nowrap;
  }
}

.licenses__item {
  max-width: 100%;
  margin-bottom: 30px;

  width: 100%;

  @include media-breakpoint-only(md) {
    .icon-link__text {
      font-size: $p2-size-md;
    }
  }

  @include media-breakpoint-up(lg) {
    // margin-right: 100px;
    margin-right: 40px;

    &:last-of-type {
      margin-right: 0;
    }

    &:nth-of-type(1) {
      width: 218px + 69px;
    }

    &:nth-of-type(2) {
      width: 225px + 69px;
    }

    &:nth-of-type(3) {
      width: 283px + 69px;
    }
  }

  @include media-breakpoint-only(lg) {
    margin-right: 20px;

    .icon-link__text {
      font-size: $p2-size-lg;
    }

    &:nth-of-type(n) {
      width: 33.3%;
    }
  }
}