// addresses-map

.addresses-map {
  padding-top: 70px;
  padding-bottom: 70px;
}

.addresses-map__title {
  margin-top: 0;
}

.addresses-map__inner {
  height: 400px;
  overflow: hidden;
  margin-top: 9px;

  @include media-breakpoint-up(sm) {
    height: 540px;
  }
}

.addresses-map__map {
  border-radius: $border-radius;
  background-color: $map-bg-color;
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
}

.addresses-map__icon-title {
  margin: 6px 0 0 32px;
  font-weight: bold;
}

.addresses-map__balloon-content {
  font-family: $font-family;
  font-size: 14px;
  padding: 10px;
  line-height: em(25px, 14px);
  font-style: normal;
  color: #0F1112;
  background: $white;
  border-radius: 8px;

  b {
    font-weight: bold;
    display: block;
    margin-bottom: 0.4em;

  }

  a {
    display: block;
    color: #0F1112;

    &:active {
      color: $link-color-hover;
    }

    @include media-breakpoint-up(sm) {

      &:hover,
      &:focus {
        color: $link-color-hover;
      }
    }
  }

  span {
    display: block;
  }

  .addresses-map__select-city {
    color: #5C6D99;
    margin-top: 10px;

    &:active {
      color: $link-color-hover;
    }

    @include media-breakpoint-up(sm) {

      &:hover,
      &:focus {
        color: $link-color-hover;
      }
    }
  }
}

.ymaps-2-1-75-balloon {
  // overflow: hidden;
}

[class*="-balloon"] {
  border-radius: 8px;
  box-shadow: none !important;
}

[class*="-balloon__tail"] {
  background: rgba(255, 255, 255, 0) !important;
}