// Ссылка с иконкой

.icon-link {
  // display: block;
  display: flex;
  align-items: center;
  position: relative;
  min-height: 50px;
  padding-left: 69px;

  &__file {
    height: 60px;
    width: 50px;
  }

  &__mail {
    height: 60px;
    width: 54px;
  }

  &__phone {
    height: 60px;
    width: 54px;
  }

  &--tel {
    padding-left: 38px;
  }

  &_small {
    min-height: 0;
    padding-left: 26px;
  }
}

.icon-link__text {
  // max-width: em(220px, 18px); // *** TEST
  display: block;
  margin-top: 0;
  margin-bottom: 0;
  // padding-top: 8px; // *** TEST

  @include link-style;
  color: $icon-link-color;

  @include media-breakpoint-up(xl) {
    font-size: 18px;
    line-height: em(24px, 18px);
  }
}

.icon-link__tel {
  display: block;
  margin-top: 0;
  margin-bottom: 0;
}

.icon-link__icon-small-wp,
.icon-link__icon-wp {
  position: absolute;
  left: 0;
}

.icon-link__icon-small-wp {
  top: 50%;
  transform: translateY(-50%);
}

.icon-link__icon-wp {
  top: 0;
  width: 69px;
  height: 50px;
}

.icon-link__icon-tel {
  width: 18px;
  height: 18px;
  top: 50%;
  transform: translateY(-50%);

}

.icon-link__icon {
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.icon-link__title {
  margin-bottom: 0;
  margin-top: 0;
}