// answer-question

.answer-question__success {
  display: none;
}

.answer-question {
  position: relative;
  overflow: hidden;

  //* вместо bg color
  &:before {
    content: '';
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $white;
    z-index: -1;
  }

  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;

  padding-top: 40px;
  padding-bottom: 17px;

  @include media-breakpoint-up(md) {
    background: url('images/banner-pattern.svg');
    background-repeat: repeat-y;
    background-position: center right;
    background-size: 25%;
  }

  @include media-breakpoint-up(md) {
    padding-top: 56px;
    padding-bottom: 44px;
    border-color: $main-light-grey;
  }

  @include media-breakpoint-up(lg) {
    background-size: 250px;
  }

  @include media-breakpoint-up(xl) {
    padding-top: 57px;
    padding-bottom: 41px;
  }


  .modal__text_small {
    @include media-breakpoint-down(sm) {
      max-width: 100%;
    }
  }
}

.answer-question__title {
  @include h2-style;
  margin-top: 0;

  @include media-breakpoint-down(ssm) {
    margin-bottom: 24px;
  }

  @include media-breakpoint-only(md) {
    font-size: 24px;
    line-height: 28px;
  }

  @include media-breakpoint-up(xl) {
    margin-top: 14px;
    margin-bottom: 26px;
  }
}

.answer-question__header {
  .answer-question__text {

    &:last-of-type {
      @include media-breakpoint-up(xl) {
        margin-bottom: 2.3em;
      }
    }
  }
}




.answer-question__text {
  line-height: 180%;

  @include media-breakpoint-only(md) {
    font-size: 14px;
    line-height: em(25px, 14px);
  }
}

.answer-question__form-wp {
  .answer-question__title {
    @include media-breakpoint-only(lg) {
      font-size: 24px;
      line-height: 28px;
    }
  }

  .answer-question__text {
    @include media-breakpoint-only(lg) {
      font-size: 14px;
      line-height: em(25px, 14px);
    }
  }
}

.answer-question__small-text {
  color: $small-text-color;
  // max-width: 380px;
  max-width: 100%;

  font-size: 12px;
  line-height: em(20px, 12px);

  &>span {
    font-size: 1em;
    margin-bottom: 0;
  }

  @include media-breakpoint-up(md) {
    max-width: 380px;
  }

  @include media-breakpoint-up(xl) {
    margin-top: 4px;
    margin-bottom: 0;
    font-size: 14px;
  }
}

.answer-question__user-agreement {
  color: $main-blue;
}

// ******************
.answer-question__form {
  &>.input {
    width: 100%;
    max-width: 100%;

    &>.input__fluid {
      width: 100%;
      max-width: 100%;
    }
  }

  @include media-breakpoint-up(md) {
    display: flex;
    align-items: flex-start;
  }

  @include media-breakpoint-between(md, lg) {
    margin-top: 4px;

    &>.input {
      width: auto;
      font-size: 14px;
      margin-right: 20px;

      &>.input__fluid {
        width: 216px;
        height: 45px;
        font-size: 14px;
      }

      &>.input__placeholder {
        margin-top: -4px;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    &>.input {
      max-width: 210px;
      margin-right: 20px;

      &>.input__fluid {
        max-width: 210px;
      }
    }
  }
}

.answer-question__btn {
  margin-top: 16px;
  margin-bottom: 14px;
  width: 100%;

  @include media-breakpoint-up(sm) {
    width: auto;
  }

  @include media-breakpoint-up(md) {
    display: block;
    margin-top: 12px;
  }

  @include media-breakpoint-between(md, lg) {
    font-size: 14px;
    height: 45px;
    padding-top: 1em;
  }

  // иначе не влазит в контейнер
  @include media-breakpoint-up(xl) {
    padding-left: 24px;
    padding-right: 24px;
  }
}


// *****************
.answer-question__success {
  @include media-breakpoint-down(sm) {
    text-align: center;
  }

  @include media-breakpoint-up(lg) {
    margin-top: 13px;
    margin-bottom: 13px;
  }

  @include media-breakpoint-up(xl) {
    margin-top: 12px;
    margin-bottom: 30px;
  }

  .answer-question__title {
    max-width: 100%;

    @include media-breakpoint-up(md) {
      margin-top: 4px;
    }

    @include media-breakpoint-up(xl) {
      margin-top: 16px;
      margin-bottom: 24px;
    }
  }

  .answer-question__text {

    @include media-breakpoint-up(md) {
      margin-bottom: 16px;
    }

    @include media-breakpoint-down(sm) {
      // max-width: 245px;
      max-width: em(245px, 14px);
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 30px;
    }

    @include media-breakpoint-up(xl) {
      margin-bottom: 13px;
    }
  }
}

.answer-question__illustration-wp {
  width: 190px;
  height: 135px;
  transform: translateX(5px);
  margin: 0 auto 50px auto;

  @include media-breakpoint-only(sm) {
    margin-bottom: 70px;
    width: 240px;
    height: 160px;
  }


  @include media-breakpoint-up(md) {
    margin-bottom: 0;
    // transform: translate(15px, -20px);
    transform: none;
    position: absolute;
    top: -30px;
    right: 16%;
  }

  @include media-breakpoint-up(lg) {
    width: 230px;
    height: 160px;
    top: -50px;
    right: 5%;
  }

  @include media-breakpoint-up(xl) {
    width: 270px;
    height: 220px;
    top: -53px;
    // right: 5%;
  }
}

.answer-question__illustration {
  height: 100%;
  max-height: 100%;
  width: 100%;
  max-width: 100%;
}

//*** 

.answer-question--rounded {
  display: none;

  .answer-question {
    border-radius: $border-radius;
    border: 1px solid $border-color;
  }

  .answer-question__success {
    display: block;
  }
}


.answer-question__footer {
  @include media-breakpoint-up(sm) {
    display: flex;
    align-items: center;
    // flex: 1 0;

    .answer-question__btn {
      margin-right: 30px;
      flex-shrink: 0;
    }

    .answer-question__small-text {
      margin-top: 12px;
      margin-bottom: 14px;
      max-width: 100%;
    }
  }

  @include media-breakpoint-up(xl) {
    // margin-top: 10px;

    .answer-question__small-text {
      // max-width: 430px;
      // max-width: 100%;
      // margin-top: 0;
    }

    .answer-question__btn {
      padding-left: 40px;
      padding-right: 40px;
      // margin-top: 0;
    }
  }
}