//contact-card

.contact-card {
  background-color: #FFFFFF;
  border: 1px solid #F2F2F2;
  box-sizing: border-box;
  border-radius: 10px;

  padding: 30px 24px;
  height: 100%;
}

.contact-card__title {
  margin-top: 0;
  margin-bottom: 1em;

  @include media-breakpoint-up(md) {
    font-size: 16px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 20px;
  }
}

.contact-card__wrap {
  position: relative;
  padding-left: 40px;
  margin-top: 0;
  margin-bottom: 1em;

  &:last-of-type {
    margin-bottom: 0;
  }
}


.contact-card__tel-wp {
  a {
    color: #0F1112;
  }
}

.contact-card__email-wp {
  a {
    color: #5C6D99;
  }
}

.contact-card__tel-wp,
.contact-card__email-wp {
  a {
    &:active {
      color: $link-color-hover;
    }

    @include media-breakpoint-up(sm) {

      &:hover,
      &:focus {
        color: $link-color-hover;
      }
    }
  }
}

.contact-card__icon-wp {
  width: 19px;
  height: 19px;
  position: absolute;
  top: .2em;
  left: 0.07em;
  display: block;
}

.contact-card__icon {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
}

.points {
  margin-top: 50px;

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
  }
}

.points__map-wp {
  height: 400px;
  overflow: hidden;

  @include media-breakpoint-up(sm) {
    height: 540px;
  }

  @include media-breakpoint-up(lg) {
    height: 704px;
  }
}

.points__icon {
  height: .6em;
  width: .6em;
  display: block;
  margin-left: .2em;
  margin-top: .16em;
  fill: #5C6D99;
  transition: fill 0.3s ease-out;
}

.points__title {
  @include media-breakpoint-between(md, lg) {
    font-size: 36px;
  }

  // @include media-breakpoint-only(lg) {
  //   font-size: 36px;
  // }

  a {
    display: inline-flex;
    align-items: center;
    flex-wrap: nowrap;
    color: #5C6D99;

    &:active {
      color: $link-color-hover;

      .points__icon {
        fill: $link-color-hover;
      }
    }

    @include media-breakpoint-up(sm) {

      &:hover,
      &:focus {
        color: $link-color-hover;

        .points__icon {
          fill: $link-color-hover;
        }
      }
    }
  }
}



.points__list {
  margin-top: 0;
}

.points__item {
  margin-bottom: 30px;

}