.modern-approach {
  // margin-bottom: 70px;

  margin-bottom: 0;

  &+.offers {
    margin-top: 50px;
  }

  &+.best-service {
    margin-top: 50px;
  }

  @include media-breakpoint-up(lg) {
    margin-top: 70px;
    margin-bottom: 0;

    &+.offers {
      margin-top: 20px;
    }

    &+.best-service {
      margin-top: 70px;
    }
  }

  &__img {
    padding-top: 72px;
    background: #EDEDF1;
    // border-top-right-radius: 10px;
    // border-top-left-radius: 10px;
    border-radius: $border-radius;

    padding: 40px 30px 0 30px;

    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;

    overflow: hidden;

    @include media-breakpoint-down(md) {
      height: 200px;

      img {
        max-height: 100%;
      }
    }

    @include media-breakpoint-down(md) {
      // padding-top: 0;
      border-radius: $border-radius;
      display: flex;
    }

    @include media-breakpoint-down(sm) {
      // width: calc(100% - 30px);
      margin-left: auto;
      margin-right: auto;
      padding-top: 30px;
    }

    img {
      margin-left: auto;
      margin-right: auto;

      @include media-breakpoint-down(md) {
        margin-top: auto;
        margin-bottom: 0;
        // border-radius: $border-radius;
      }

      @include media-breakpoint-down(sm) {
        border-radius: 0;
      }

    }
  }



  .row {
    // flex-wrap: nowrap;

    // @include media-breakpoint-down(sm) {
    //   flex-direction: column;
    // }
  }
}

.certification-center__banner+.modern-approach .modern-approach__img {
  @include media-breakpoint-down(md) {
    // padding-top: 24px;
    padding: 24px 30px 0 30px;
  }


}

.modern-approach {

  .modern-approach__img {

    // *** Чтобы картинка была по центру, а не снизу
    &.modern-approach__img--center {
      align-items: center;
      padding-top: 10px;
      padding-bottom: 10px;

      &>img {
        margin-top: auto;
        margin-bottom: auto;
      }
    }

    // *** Чтобы установить фоновое изображение
    &.modern-approach__img--graph-bg {
      background-image: url('images/graph-thin-bg.svg');
      background-repeat: no-repeat;
      background-position: center bottom -40px;
      background-size: 90% auto;
    }

  }

  // если блок не отображён по горизонтали
  &:not(.reverse) {
    .modern-approach__text {
      margin-left: 54px;

      @include media-breakpoint-down(lg) {
        margin-left: 44px;
      }

      @include media-breakpoint-down(md) {
        // margin-left: 34px;
        margin-left: 0;
      }

      // @include media-breakpoint-down(sm) {
      //   margin-left: 0;
      // }

      // убрать нижний отступ у последнего текстового элемента (параграфа)
      &>p:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  // поменять блоки местами (отобразить по горизонтали)
  &.reverse {
    .modern-approach__img {
      margin-top: 50px;

      @include media-breakpoint-up(md) {
        margin-top: 0;
      }
    }
  }
}

.modern-approach__text-wp {
  display: flex;
}

.modern-approach__text {
  margin-top: auto;
  margin-bottom: auto;

  ul {
    margin-bottom: 0;
  }

  h2 {
    margin-top: 0;
    margin-bottom: 40px;

    @include media-breakpoint-down(lg) {
      margin-bottom: 30px;
    }

    @include media-breakpoint-down(md) {
      margin-top: 50px;
    }
  }

  ul {
    >li {
      position: relative;
      font-size: 16px;
      line-height: 1.8125em;
      padding-left: 1.3em;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 180%;

      &:before {
        content: '';
        position: absolute;
        display: inline-block;
        height: 5px;
        width: 5px;
        border-radius: 50%;
        background-color: $main-yellow;
        left: 0;
        top: .65em;
      }
    }

    li+li {
      margin-top: 15px;
    }
  }
}