// search-steps

.form-step {
  display: none;
}

.search-steps {
  margin-top: 50px;
  margin-bottom: 50px;

  @include media-breakpoint-up(md) {
    margin-top: 70px;
    margin-bottom: 70px;

  }

  .cost-calc__body {

    @include media-breakpoint-down(sm) {
      padding-top: 23px;
    }
  }
}


.regions-container {
  align-items: flex-start;

  .region {
    &:last-of-type {
      .region__main-checkbox {
        margin-bottom: 0;
      }

      .region__content {
        top: 100%;
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .region {
      width: calc((100% / 2) - 10px);

      &:not(:nth-of-type(2n)) {
        margin-right: 20px;
      }
    }
  }
}