// advantages-order

.organization-benefits {
  margin-bottom: 70px;
  @include media-breakpoint-down(lg) {
    margin-bottom: 60px;
  }
  @include media-breakpoint-down(md) {
    margin-bottom: 50px;
  }
}

.organization-benefits__subtitle {
  margin-top: 14px;
  & + p{
    margin-bottom: 0;
  }
}

.organization-benefits__list {
  margin-top: 10px;
}

.organization-benefits__item {
  margin-bottom: 20px;
}

.organization-benefits__link {
  margin-top: 23px;
}

.bid__link{
  color: black;
  &:hover{
    color: black;
  }
}